import { Stage } from '..';

class History {
  #stage: Stage;
  #isOver: Boolean = true;
  #isRecording: Boolean = false;
  #history = [] as Array<any>;
  #firstRecord: MutationRecord[] = [];
  #endRecord: MutationRecord[] = [];
  #record: MutationRecord[] = [];
  #index = 0;
  #listen?: MutationObserver;
  #action?: Symbol;
  #CN_label?: string;
  #actions = {
    ADD: Symbol(),
    DELETE: Symbol(),
    NODE_WIDTH: Symbol(),
    NODE_HEIGHT: Symbol(),
    SCALE: Symbol(),
    MOUSE_SCALE: Symbol(),
    NODE_X: Symbol(),
    NODE_Y: Symbol(),
    MOUSE_MOVE: Symbol(),
    KEYBOARD_MOVE: Symbol(),
    TITLE: Symbol(),
    EDIT: Symbol(),
    REORDER: Symbol(),
    GROUP: Symbol(),
    UNGROUP: Symbol(),
  };
  #callBack: Array<Function> = [];
  constructor(stage: any) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
    this.#updated();
  }
  watch = (func: Function) => {
    this.#callBack?.push(func);
  };
  #updated = () => {
    this.#listen = new MutationObserver((record) => {
      if (!this.#isRecording) return;
      if (this.#firstRecord.length === 0) {
        this.#firstRecord = record;
      }
      this.#endRecord = record;

      if (this.#isOver) {
        this.#history.splice(this.#index - 1, 1, {
          record,
          action: this.#action,
          CN_label: this.#CN_label,
        });
      }
      this.#callBack?.forEach((func: Function) => {
        func();
      });
    });

    this.#listen.observe(this.#stage.svgRoot as Element, {
      childList: true,
      subtree: true,
      attributes: true,
      attributeOldValue: true,
    });
  };
  add = (action: Symbol, CN_label?: string) => {
    if (!action) throw new Error('action not found!');
    this.#firstRecord = [];
    this.#endRecord = [];
    this.#isOver = false;
    this.#isRecording = true;
    this.#action = action;
    this.#CN_label = CN_label;
    const dC = this.#history.length - this.#index;

    this.#history.splice(this.#index, dC, {
      action,
      CN_label,
    });

    this.#index++;
  };
  end = () => {
    if (!this.#isRecording) return;
    this.#isOver = true;
    setTimeout(() => {
      this.#isRecording = false;
    });
  };
  undo = () => {
    if (!this.has(this.#index - 1)) return;
    this.#index--;
    const { record, action, CN_label } = this.item(this.#index);

    const mouseActions = [
      this.actions().SCALE,
      this.actions().MOUSE_MOVE,
      this.actions().MOUSE_SCALE,
      this.actions().KEYBOARD_MOVE,
    ];

    record?.reverse()?.forEach((r: MutationRecord) => {
      const { addedNodes, removedNodes, type, oldValue } = r;
      const t = r.target as SVGElement;
      const nextSibling = r.nextSibling as SVGElement;
      const attributeName = r.attributeName || '';
      (r as any).newValue = t.getAttribute(attributeName) || null;
      if (type === 'attributes') {
        if (
          !(t.localName !== 'g' && attributeName === 'transform' && mouseActions.includes(action))
        ) {
          if (oldValue) {
            t.setAttribute(attributeName, oldValue);
          } else {
            t.removeAttribute(attributeName);
          }
        }
      }

      addedNodes.forEach((node: Node) => {
        const n = node as SVGElement;
        this.#stage.select?.delete(n);
        n.remove();
      });
      removedNodes.forEach((node: Node) => {
        const n = node as SVGElement;
        this.#stage.select?.set(node, { node });
        if (nextSibling) {
          nextSibling.before(n);
        } else {
          t.append(n);
        }
      });
    });

    this.#history.splice(this.#index, 1, {
      record: record?.reverse(),
      action,
      CN_label,
    });

    this.#stage.select?.resetFrame(true);

    this.#callBack?.forEach((func: Function) => {
      func('undo');
    });
  };
  redo = () => {
    if (!this.has(this.#index)) return;
    const { record, action } = this.item(this.#index);

    const mouseActions = [
      this.actions().SCALE,
      this.actions().MOUSE_MOVE,
      this.actions().MOUSE_SCALE,
      this.actions().KEYBOARD_MOVE,
    ];

    record?.forEach((r: MutationRecord) => {
      const { addedNodes, removedNodes, type } = r;
      const t = r.target as SVGElement;
      const nextSibling = r.nextSibling as SVGElement;
      const attributeName = r.attributeName || '';

      if (type === 'attributes') {
        if (
          !(t.localName !== 'g' && attributeName === 'transform' && mouseActions.includes(action))
        ) {
          const newValue = (r as any).newValue;
          if (newValue) {
            t.setAttribute(attributeName, newValue);
          } else {
            t.removeAttribute(attributeName);
          }
        }
      }

      addedNodes.forEach((node: Node) => {
        const n = node as SVGElement;
        this.#stage.select?.set(node, { node });
        if (nextSibling) {
          nextSibling.before(n);
        } else {
          t.append(n);
        }
      });
      removedNodes.forEach((node: Node) => {
        const n = node as SVGElement;
        this.#stage.select?.delete(n);
        n.remove();
      });
    });

    this.#stage.select?.resetFrame(true);
    this.#index++;
    this.#callBack?.forEach((func: Function) => {
      func('redo');
    });
  };
  clear = () => {
    this.#history.splice(0, this.#history.length);
  };
  item = (index: number) => {
    return this.#history[index];
  };
  get = () => {
    return this.#history;
  };
  size = () => {
    return this.#history.length;
  };
  has = (index: number) => {
    try {
      return this.#history[index];
    } catch {
      return false;
    }
  };
  push = (c: any) => {
    this.#history.push(c);
  };
  setIndex = (index: number) => {
    this.#index = index;
  };
  index = () => {
    return this.#index;
  };
  actions = () => {
    return this.#actions;
  };
  removeWatch = (func: Function) => {
    const index = this.#callBack.findIndex((f) => f === func);
    this.#callBack.splice(index, 1);
  };
  uninstall = () => {
    this.#callBack = [];
  };
}
export default History;
