import { Stage } from '..';
class Scale {
  #stage: Stage;
  #isDisabeld: Boolean = false;
  constructor(stage: Stage) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
    this.#stage.stage?.addEventListener('wheel', this.#wheel, { passive: false });
  }
  #wheel = (ev: WheelEvent) => {
    if (this.#isDisabeld) return;
    ev.stopPropagation();
    const m = this.#stage.matrix;
    if (m.a >= 3000) {
      m.a = 3000;
    }
    if (m.a <= 0.001) {
      m.a = 0.001;
    }

    const d = (m.a / 100) * 10;
    const box = this.#stage.boardView?.getBox();
    const dx = (ev.offsetX - box.x) * d * (1 / m.a);
    const dy = (ev.offsetY - box.y) * d * (1 / m.a);
    m.e = ev.deltaY < 0 ? m.e - dx : dx + m.e;
    m.f = ev.deltaY < 0 ? m.f - dy : m.f + dy;
    ev.deltaY < 0 ? (m.a += d) : (m.a -= d);

    this.#stage.canvas?.setAttribute('transform', `matrix(${m.a},0,0,${m.a},${m.e},${m.f})`);
    this.#stage.select?.resetFrame(false);
  };
  disabled = (isDisabled: boolean) => {
    this.#isDisabeld = isDisabled;
  };
  uninstall = () => {
    this.#stage.stage?.removeEventListener('wheel', this.#wheel);
  };
}
export default Scale;
