import "./index.css";
import React from "react";
import { Stage } from "../../../lux";

class Fill extends React.Component {
  colorChange = (ev: any) => {
    this.chageAttribute("fill", ev.target.value);
  };

  opacitySliderChange = (ev: any) => {
    this.chageAttribute("opacity", ev.target.value);
    const opacityInput = document.querySelector("#opacity-input");
    opacityInput?.setAttribute("value", ev.target.value);
  };

  opacityInputChange = (ev: any) => {
    this.chageAttribute("opacity", ev.target.value);
    const opacitySlider = document.querySelector("#opacity-slider");
    opacitySlider?.setAttribute("value", ev.target.value);
  };

  chageAttribute = (type: string, color: string) => {
    const stage = (window as any).stage;
    stage.history?.add(stage.history.actions().EDIT, type);
    stage.select?.get().forEach((item: any) => {
      const node = item.node;
      const treeWalker = document.createTreeWalker(
        node,
        NodeFilter.SHOW_ELEMENT
      );
      let cn = treeWalker.currentNode as any;
      while (cn) {
        if (cn.localName !== "g") {
          (cn as SVGElement).setAttribute(type, color);
        }
        cn = treeWalker.nextNode();
      }
    });

    stage.history?.end();
  };

  watch = () => {
    const stage = (window as any).stage as Stage;
    const opacitySlider= document.querySelector('#opacity-slider');
    const opacityInput = document.querySelector('#opacity-input');
    if (stage.select?.size() === 0) {
      opacitySlider?.setAttribute('disabled', "");
      opacityInput?.setAttribute('disabled', "");
    } else { 
      opacitySlider?.removeAttribute('disabled');
      opacityInput?.removeAttribute('disabled');
    }
    try {
      stage.select?.get().forEach((item: any) => {
        const node = item.node;
        const treeWalker = document.createTreeWalker(
          node,
          NodeFilter.SHOW_ELEMENT
        );
        let cn = treeWalker.currentNode as any;
        while (cn) {
          if (cn.localName !== "g") {
            const rectPicker = document.querySelector('#rect-picker');
            const opacitySlider = document.querySelector('#opacity-slider');
            const opacityInput = document.querySelector('#opacity-input');
            const fill = window.getComputedStyle(node).fill;
            const opacity = window.getComputedStyle(node).opacity;
            rectPicker?.setAttribute('value', fill);
            opacitySlider?.setAttribute('value', opacity);
            opacityInput?.setAttribute('value', opacity);
            throw new Error('ok color')
          }
          cn = treeWalker.nextNode();
        }
      });
     } catch (error) { }
  

  };

  getAttrFirst = (node: any, attr: any) => {
    let arrtVal;
    if (node.hasAttribute(attr) && node.nodeName !== "g") {
      arrtVal = node.getAttribute(attr);
    }
    if (node.children.length) {
      for (let i = 0; i < node.children.length; i++) {
        if (
          node.children[i].hasAttribute(attr) &&
          node.children[i].nodeName !== "g"
        ) {
          arrtVal = node.children[i].getAttribute(attr);
        } else {
          const result = this.getAttrFirst(node.children[i], attr) as any;
          if (result) {
            arrtVal = result;
          }
        }
      }
    }
    return arrtVal;
  };

  init = () => {
    const colorPicker = document.querySelector("#rect-picker");
    colorPicker?.addEventListener("changeend", this.colorChange);
    const opacitySlider = document.querySelector("#opacity-slider");
    opacitySlider?.addEventListener("changeend", this.opacitySliderChange);
    const opacityInput = document.querySelector("#opacity-input");
    opacityInput?.addEventListener("change", this.opacityInputChange);
    opacityInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });

    const stage = (window as any).stage as Stage;
    stage.watch(stage.actions().SELECTED, this.watch);
  };

  componentDidMount() {
    this.init();
  }

  componentWillUnmount() {}

  render = () => {
    return (
      <div id="fill">
        <h2>
          <x-message href="#fill">Fill</x-message>
        </h2>
        <section id="paint-section">
          <h3 id="section-heading">
            <x-message href="#paint">Paint</x-message>
          </h3>
          <x-rectcolorpicker
            id="rect-picker"
            value="#bada55"
            alphaslider=""
          ></x-rectcolorpicker>
        </section>
        <hr />
        <div id="opacity-section">
          <h3 id="opacity-section-heading">
            <x-message>Opacity</x-message>
          </h3>
          <div className="box">
            <x-slider
              id="opacity-slider"
              value="0"
              min="0"
              max="1"
              step="0.01"
              disabled=""
            ></x-slider>
            <x-numberinput
              id="opacity-input"
              value="0"
              min="0"
              max="1"
              step="0.01"
              condensed=""
              role="input"
              tabindex="0"
              disabled=""
            >
              <x-stepper></x-stepper>
            </x-numberinput>
          </div>
        </div>
      </div>
    );
  };
}

export default Fill;
