import React from "react";
import { Stage } from "../../lux";

interface State {
  stage: Stage | undefined;
}

interface Props {}

class Contextmenu extends React.Component {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      stage: void 0,
    };
  }

  action = (ev: MouseEvent) => {
    const { target } = ev;
    switch ((target as Element).textContent) {
      case "Undo":
        this.state.stage?.history?.undo();
        break;
      case "Redo":
        this.state.stage?.history?.redo();
        break;
      case "Cut":
        this.state.stage?.clipboard?.cut();
        break;
      case "Copy":
        this.state.stage?.clipboard?.copy();
        break;
      case "Paste":
        this.state.stage?.clipboard?.paste();
        break;
      case "Delete":
        this.state.stage?.select?.nodesRemove();
        break;
      case "Group":
        this.state.stage?.group();
        break;
      case "Ungroup":
        this.state.stage?.unGroup();
        break;
      case "Raise":
        this.state.stage?.raise();
        break;
      case "Lower":
        this.state.stage?.lower();
        break;
      case "Front":
        this.state.stage?.front();
        break;
      case "Back":
        this.state.stage?.back();
        break;
    }
  };

  contextmenu = async (ev: MouseEvent) => {
    const hasGTag = this.state.stage?.select
      ?.checkedsGroups()
      .keys()
      .next().value;

    const isC: Boolean = (() => {
      const { left, top, bottom, right } = this.state.stage?.select
        ?.frame()
        ?.getBoundingClientRect() as DOMRect;
      const x = ev.clientX;
      const y = ev.clientY;
      if (x >= left && x <= right && y >= top && y <= bottom) return true;
      return false;
    })();

    let hasClipboard: any;
    try {
      hasClipboard = await window.navigator.clipboard.read();
    } catch (error) {
      hasClipboard = false;
    }

    const cs = this.state.stage?.select?.size();
    const contextmenu = document.querySelector("#contextmenu");
    const menuitem = contextmenu?.querySelectorAll("x-menuitem");
    menuitem?.forEach((node) => {
      node.removeAttribute("disabled");
      if (
        node.textContent === "Cut" ||
        node.textContent === "Copy" ||
        node.textContent === "Group" ||
        node.textContent === "Delete" ||
        node.textContent === "Raise" ||
        node.textContent === "Lower" ||
        node.textContent === "Front" ||
        node.textContent === "Back"
      ) {
        if (cs === 0 || !isC) node.toggleAttribute("disabled");
      }
      if (node.textContent === "Ungroup") {
        if (!hasGTag || !isC) node.toggleAttribute("disabled");
      }
      if (node.textContent === "Paste") {
        if (!hasClipboard) node.toggleAttribute("disabled");
      }
    });
  };

  componentDidMount() {
    this.setState({
      stage: (window as any).stage,
    });

    document.addEventListener("contextmenu", this.contextmenu);
  }

  componentDidUpdate(
    prevProps: Readonly<Props>,
    prevState: Readonly<State>,
    snapshot?: any
  ): void {
    // if (!prevState.stage) this.init();
  }

  componentWillUnmount() {}

  render = () => {
    return (
      <x-contextmenu id="contextmenu">
        <x-menu>
          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Undo</x-label>
          </x-menuitem>

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Redo</x-label>
          </x-menuitem>

          <hr />

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Cut</x-label>
          </x-menuitem>

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Copy</x-label>
          </x-menuitem>

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Paste</x-label>
          </x-menuitem>

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Delete</x-label>
          </x-menuitem>

          <hr />

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Group</x-label>
          </x-menuitem>

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Ungroup</x-label>
          </x-menuitem>

          <hr />

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Raise</x-label>
          </x-menuitem>

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Lower</x-label>
          </x-menuitem>

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Front</x-label>
          </x-menuitem>

          <x-menuitem
            onClick={(ev: any) => {
              this.action(ev);
            }}
          >
            <x-label>Back</x-label>
          </x-menuitem>
        </x-menu>
      </x-contextmenu>
    );
  };
}

export default Contextmenu;
