import "./index.css";
import React from "react";
import { Stage } from "../../../lux";

class Stroke extends React.Component {
  colorChange = (ev: any) => {
    this.chageAttribute("stroke", ev.target.value);
  };

  opacitySliderChange = (ev: any) => {
    this.chageAttribute("stroke-opacity", ev.target.value);
    const opacityInput = document.querySelector("#opacity-width-input");
    opacityInput?.setAttribute("value", ev.target.value);
  };

  opacityInputChange = (ev: any) => {
    this.chageAttribute("stroke-opacity", ev.target.value);
    const opacitySlider = document.querySelector("#opacity-width-slider");
    opacitySlider?.setAttribute("value", ev.target.value);
  };

  strokeWidthSliderChange = (ev: any) => {
    this.chageAttribute("stroke-width", ev.target.value);
    const strokeWidthInput = document.querySelector("#stroke-width-input");
    strokeWidthInput?.setAttribute("value", ev.target.value);
  };

  strokeWidthInputChange = (ev: any) => {
    this.chageAttribute("stroke-width", ev.target.value);
    const strokeWidthSlider = document.querySelector("#stroke-width-slider");
    strokeWidthSlider?.setAttribute("value", ev.target.value);
  };

  chageAttribute = (type: string, color: string) => {
    const stage = (window as any).stage;
    stage.history?.add(stage.history.actions().EDIT, type);
    stage.select?.get().forEach((item: any) => {
      const node = item.node;
      const treeWalker = document.createTreeWalker(
        node,
        NodeFilter.SHOW_ELEMENT
      );
      let cn = treeWalker.currentNode as any;
      while (cn) {
        if (cn.localName !== "g") {
          (cn as SVGElement).setAttribute(type, color);
        }
        cn = treeWalker.nextNode();
      }
    });

    stage.history?.end();
  };

  watch = () => {
    const stage = (window as any).stage as Stage;
    const opacityWidthSlider = document.querySelector("#opacity-width-slider");
    const opacityWidthInput = document.querySelector("#opacity-width-input");
    const strokeWidthSlider = document.querySelector("#stroke-width-input");
    const strokeWidthInput = document.querySelector("#stroke-width-slider");
    if (stage.select?.size() === 0) {
      opacityWidthSlider?.setAttribute("disabled", "");
      opacityWidthInput?.setAttribute("disabled", "");
      strokeWidthSlider?.setAttribute("disabled", "");
      strokeWidthInput?.setAttribute("disabled", "");
    } else {
      opacityWidthSlider?.removeAttribute("disabled");
      opacityWidthInput?.removeAttribute("disabled");
      strokeWidthSlider?.removeAttribute("disabled");
      strokeWidthInput?.removeAttribute("disabled");
    }
    try {
      stage.select?.get().forEach((item: any) => {
        const node = item.node;
        const treeWalker = document.createTreeWalker(
          node,
          NodeFilter.SHOW_ELEMENT
        );
        let cn = treeWalker.currentNode as any;
        while (cn) {
          if (cn.localName !== "g") {
            const rectPicker = document.querySelector("#rect-stroke-picker");
            const opacitySlider = document.querySelector(
              "#opacity-width-slider"
            );
            const opacityInput = document.querySelector("#opacity-width-input");
            const strokeWidthSlider = document.querySelector(
              "#stroke-width-input"
            );
            const strokeWidthInput = document.querySelector(
              "#stroke-width-slider"
            );
            const stroke = window.getComputedStyle(node).stroke;
            const strokeOpacity = window.getComputedStyle(node).strokeOpacity;
            const strokeWidth = parseFloat(
              window.getComputedStyle(node).strokeWidth
            );
            rectPicker?.setAttribute("value", stroke);
            opacitySlider?.setAttribute("value", strokeOpacity);
            opacityInput?.setAttribute("value", strokeOpacity);
            strokeWidthSlider?.setAttribute("value", strokeWidth.toString());
            strokeWidthInput?.setAttribute("value", strokeWidth.toString());
            throw new Error("ok color");
          }
          cn = treeWalker.nextNode();
        }
      });
    } catch (error) {}
  };

  getAttrFirst = (node: any, attr: any) => {
    let arrtVal;
    if (node.hasAttribute(attr) && node.nodeName !== "g") {
      arrtVal = node.getAttribute(attr);
    }
    if (node.children.length) {
      for (let i = 0; i < node.children.length; i++) {
        if (
          node.children[i].hasAttribute(attr) &&
          node.children[i].nodeName !== "g"
        ) {
          arrtVal = node.children[i].getAttribute(attr);
        } else {
          const result = this.getAttrFirst(node.children[i], attr) as any;
          if (result) {
            arrtVal = result;
          }
        }
      }
    }
    return arrtVal;
  };

  init = () => {
    const colorPicker = document.querySelector("#rect-stroke-picker");
    colorPicker?.addEventListener("changeend", this.colorChange);
    const opacitySlider = document.querySelector("#opacity-width-slider");
    opacitySlider?.addEventListener("changeend", this.opacitySliderChange);
    const opacityInput = document.querySelector("#opacity-width-input");
    opacityInput?.addEventListener("change", this.opacityInputChange);
    opacityInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });
    const strokeWidthSlider = document.querySelector("#stroke-width-slider");
    strokeWidthSlider?.addEventListener(
      "changeend",
      this.strokeWidthSliderChange
    );
    const strokeWidthInput = document.querySelector("#stroke-width-input");
    strokeWidthInput?.addEventListener("change", this.strokeWidthInputChange);
    strokeWidthInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });

    const stage = (window as any).stage as Stage;
    stage.watch(stage.actions().SELECTED, this.watch);
  };

  componentDidMount() {
    this.init();
  }

  componentWillUnmount() {}

  render = () => {
    return (
      <div id="stroke">
        <h2>
          <x-message>Stroke</x-message>
        </h2>
        <section id="paint-section">
          <h3 id="section-heading">
            <x-message>Paint</x-message>
          </h3>
          <x-rectcolorpicker
            id="rect-stroke-picker"
            value="#bada55"
            alphaslider=""
          ></x-rectcolorpicker>
        </section>
        <hr />
        <div id="opacity-width-section">
          <h3 id="opacity-width-section-heading">
            <x-message>Opacity</x-message>
          </h3>
          <div className="box">
            <x-slider
              id="opacity-width-slider"
              value="0"
              min="0"
              max="1"
              step="0.01"
            ></x-slider>
            <x-numberinput
              id="opacity-width-input"
              value="0"
              min="0"
              max="1"
              step="0.01"
              condensed=""
              role="input"
              tabindex="0"
            >
              <x-stepper></x-stepper>
            </x-numberinput>
          </div>
        </div>
        <hr />
        <div id="stroke-width-section">
          <h3 id="stroke-width-section-heading">
            <x-message>Width</x-message>
          </h3>
          <div className="box">
            <x-slider
              id="stroke-width-slider"
              value="0"
              min="0"
              max="50"
              step="1"
            ></x-slider>
            <x-numberinput
              id="stroke-width-input"
              value="0"
              min="0"
              max="50"
              step="1"
              condensed=""
              role="input"
              tabindex="0"
            >
              <x-stepper></x-stepper>
            </x-numberinput>
          </div>
        </div>
      </div>
    );
  };
}

export default Stroke;
