import Draggable from '../dependencies/draggable';
import { Stage } from '..';
class View {
  #stage: Stage;
  #isDraging = false;
  #viewHud?: SVGElement;
  #subhuds?: SVGElement;
  #view?: SVGElement;
  #viewObserver?: MutationObserver;
  #cacheX = 0;
  #cacheY = 0;
  #cacheWidth = 0;
  #cacheHeight = 0;
  #draggable?: Draggable;
  #isDisabled: Boolean = false;
  #callback?: Function;
  constructor(stage: Stage) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
    this.#install();
    this.#adjustViewGrippie();
    this.#listenViewResize();
  }
  #install() {
    this.#viewHud = document.querySelector('#view-hud') as SVGElement;
    this.#subhuds = this.#viewHud?.querySelector("[uid='subhuds']") as SVGElement;
    this.#view = document.querySelector('#background-outlines rect') as SVGElement;
    this.#initEvent();
    this.#stage.watch(this.#stage.actions().SCALE, this.#scaleWatch);
  }
  #scaleWatch = () => {
    this.#adjustViewGrippie();
  };
  #initEvent = () => {
    this.#subhuds?.childNodes.forEach((ChildNode: ChildNode) => {
      if (ChildNode.nodeType === 3) return;
      const element = ChildNode as SVGElement;
      this.#draggable = new Draggable();
      this.#draggable?.install({
        element,
        start: () => {
          this.#isDraging = true;
          const { e, f } = element.getMatrix();
          element.setAttribute('e', e);
          element.setAttribute('f', f);
          this.#cacheWidth = Number(this.#view?.getAttribute('width'));
          this.#cacheHeight = Number(this.#view?.getAttribute('height'));
          this.#cacheX = Number(this.#view?.getAttribute('x'));
          this.#cacheY = Number(this.#view?.getAttribute('y'));
        },
        move: (ev: Draggable) => {
          if (!this.#isDraging) return;
          const pos = element.getAttribute('data-position');
          const dx = ev.dataX / this.#stage.matrix.a;
          const dy = ev.dataY / this.#stage.matrix.d;

          switch (pos) {
            case 'top-left':
              this.#view?.setAttribute('x', (this.#cacheX + dx).toFixed(9));
              this.#view?.setAttribute('width', (this.#cacheWidth - dx).toFixed(9));
              this.#view?.setAttribute('y', (this.#cacheY + dy).toFixed(9));
              this.#view?.setAttribute('height', (this.#cacheHeight - dy).toFixed(9));
              break;
            case 'top-right':
              this.#view?.setAttribute('width', (this.#cacheWidth + dx).toFixed(9));
              this.#view?.setAttribute('y', (this.#cacheY + dy).toFixed(9));
              this.#view?.setAttribute('height', (this.#cacheHeight - dy).toFixed(9));
              break;
            case 'bottom-left':
              this.#view?.setAttribute('x', (this.#cacheX + dx).toFixed(9));
              this.#view?.setAttribute('width', (this.#cacheWidth - dx).toFixed(9));
              this.#view?.setAttribute('height', (this.#cacheHeight + dy).toFixed(9));
              break;
            case 'bottom-right':
              this.#view?.setAttribute('width', (this.#cacheWidth + dx).toFixed(9));
              this.#view?.setAttribute('height', (this.#cacheHeight + dy).toFixed(9));
              break;
          }
        },
        end: () => {
          if (!this.#isDraging) return;
          this.#isDraging = false;
        },
      });
    });
  };
  watch = (func: Function) => {
    this.#callback = func;
  };
  #listenViewResize = () => {
    this.#viewObserver = new MutationObserver(() => {
      this.#adjustViewGrippie();
      this.#callback?.();
    });
    if (this.#view)
      this.#viewObserver.observe(this.#view, {
        attributes: true,
        attributeFilter: ['x', 'y', 'width', 'height'],
      });
  };
  #adjustViewGrippie = () => {
    if (this.#isDisabled) return;
    const { x, y, right, bottom } = this.#view?.getBoundingClientRect() as DOMRect;
    const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
    const { a, d, e, f } = this.#stage.matrix;

    const padding = 15;
    this.#subhuds?.childNodes.forEach((ChildNode: ChildNode) => {
      if (ChildNode.nodeType === 3) return;
      const element = ChildNode as SVGElement;
      const pos = element.getAttribute('data-position');
      let ce = 0;
      let cf = 0;
      const sx = 1 / a;
      const sy = 1 / d;
      switch (pos) {
        case 'top-left':
          ce = (x - left - e) / a - sx * padding;
          cf = (y - top - f) / d - sy * padding;
          break;
        case 'top-right':
          ce = (right - left - e) / a - sx * padding;
          cf = (y - top - f) / d - sy * padding;
          break;
        case 'bottom-left':
          ce = (x - left - e) / a - sx * padding;
          cf = (bottom - top - f) / d - sy * padding;
          break;
        case 'bottom-right':
          ce = (right - left - e) / a - sx * padding;
          cf = (bottom - top - f) / d - sy * padding;
          break;
      }
      element.setAttribute('transform', `matrix(${sx},0,0,${sy},${ce},${cf})`);
    });
  };
  disabled = (isDisabled: boolean) => {
    this.#isDisabled = isDisabled;
    if (!this.#viewHud) return;
    this.#viewHud.setAttribute('style', `visibility : ${isDisabled ? 'hidden' : 'visible'}`);
    this.#adjustViewGrippie();
  };
  uninstall = () => {
    this.#stage.removeWatch(this.#stage.actions().SCALE, this.#scaleWatch);
    this.#draggable?.uninstall();
    this.#viewObserver?.disconnect();
  };
}
export default View;
