import "./index.css";
import React from "react";
import { Stage } from "../../../lux";

class Arrangement extends React.Component {
  nodeAlign = (direction: string, align: string) => {
    const stage = (window as any).stage as Stage;
    stage.nodeAlign(direction, align);
  };

  transform = (direction: string) => {
    const stage = (window as any).stage as Stage;
    stage.filp(direction);
  };

  raise = () => {
    const stage = (window as any).stage as Stage;
    stage.raise();
  };

  lower = () => {
    const stage = (window as any).stage as Stage;
    stage.lower();
  };

  front = () => {
    const stage = (window as any).stage as Stage;
    stage.front();
  };

  back = () => {
    const stage = (window as any).stage as Stage;
    stage.back();
  };

  group = () => {
    const stage = (window as any).stage as Stage;
    stage.group();
  };

  unGroup = () => {
    const stage = (window as any).stage as Stage;
    stage.unGroup();
  };

  watch = () => {
    const stage = (window as any).stage as Stage;
    const arrangement = document.querySelector("#arrangement");
    const xBtns = arrangement?.querySelectorAll("x-button");
    xBtns?.forEach((btn) => {
      if (Number(stage.select?.size()) > 0) {
        btn.removeAttribute("disabled");
      } else {
        btn.setAttribute("disabled","");
      }
    });
  };

  init = () => {
    const stage = (window as any).stage as Stage;
    stage.watch(stage.actions().SELECTED, this.watch);
  };

  componentDidMount() {
    this.init();
  }

  componentWillUnmount() {}

  render = () => {
    return (
      <div id="arrangement">
        <main>
          <h2>
            <x-message>Arrangement</x-message>
          </h2>
          <section id="align-section">
            <h3 id="align-section-heading">
              <x-message>Alignment</x-message>
            </h3>
            <section className="flex flex-justify-between">
              <x-button
                id="align-top-button"
                value="move-top-inside"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.nodeAlign("h", "left")}
              >
                <x-icon href="#align-left"></x-icon>
                <x-tooltip>
                  <x-message>Align left</x-message>
                </x-tooltip>
              </x-button>
              <x-button
                id="align-top-button"
                value="move-top-inside"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.nodeAlign("v", "top")}
              >
                <x-icon href="#align-top"></x-icon>
                <x-tooltip>
                  <x-message>Align top</x-message>
                </x-tooltip>
              </x-button>
              <x-button
                id="align-top-button"
                value="move-top-inside"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.nodeAlign("h", "right")}
              >
                <x-icon href="#align-right"></x-icon>
                <x-tooltip>
                  <x-message>Align right</x-message>
                </x-tooltip>
              </x-button>
              <x-button
                id="align-top-button"
                value="move-top-inside"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.nodeAlign("v", "bottom")}
              >
                <x-icon href="#align-bottom"></x-icon>
                <x-tooltip>
                  <x-message>Align bottom</x-message>
                </x-tooltip>
              </x-button>
              <x-button
                id="align-top-button"
                value="move-top-inside"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.nodeAlign("h", "center")}
              >
                <x-icon href="#center-horizontally"></x-icon>
                <x-tooltip>
                  <x-message>Center horizontally</x-message>
                </x-tooltip>
              </x-button>
              <x-button
                id="align-top-button"
                value="move-top-inside"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.nodeAlign("v", "center")}
              >
                <x-icon href="#center-vertically"></x-icon>
                <x-tooltip>
                  <x-message>Center vertically</x-message>
                </x-tooltip>
              </x-button>
            </section>
          </section>
          <hr />
          <section id="transform-section">
            <h3 id="transform-heading">
              <x-message>Transform</x-message>
            </h3>
            <x-box id="flip-buttons">
              <x-button
                id="flip-horizontally-button"
                value="flipX"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.transform("x")}
              >
                <x-icon href="#flip-x"></x-icon>
                <x-label>
                  <x-message>Flip X</x-message>
                </x-label>
              </x-button>
              <x-button
                id="flip-vertically-button"
                value="flipY"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.transform("y")}
              >
                <x-icon href="#flip-y"></x-icon>
                <x-label>
                  <x-message>Flip Y</x-message>
                </x-label>
              </x-button>
            </x-box>
          </section>
          <hr />
          <section id="order-section">
            <h3 id="order-section-heading">
              <x-message>Order</x-message>
            </h3>
            <x-box id="order-buttons-1">
              <x-button
                id="raise-button"
                value="raise"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.raise()}
              >
                <x-icon href="#raise"></x-icon>
                <x-label>
                  <x-message>Raise</x-message>
                </x-label>
              </x-button>
              <x-button
                id="lower-button"
                value="lower"
                condensed=""
                role="button"
                aria-disabled="true"
                tabindex="-1"
                skin="default"
                onClick={() => this.lower()}
              >
                <x-icon href="#lower"></x-icon>
                <x-label>
                  <x-message>Lower</x-message>
                </x-label>
              </x-button>
            </x-box>
            <x-box id="order-buttons-2">
              <x-button
                id="front-button"
                value="raiseToFront"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.front()}
              >
                <x-icon href="#raise-to-front"></x-icon>
                <x-label>
                  <x-message>Front</x-message>
                </x-label>
              </x-button>
              <x-button
                id="back-button"
                value="lowerToBack"
                condensed=""
                role="button"
                aria-disabled="true"
                tabindex="-1"
                skin="default"
                onClick={() => this.back()}
              >
                <x-icon href="#lower-to-back"></x-icon>
                <x-label>
                  <x-message>Back</x-message>
                </x-label>
              </x-button>
            </x-box>
          </section>
          <hr />
          <section id="grouping-section">
            <h3 id="grouping-section-heading">
              <x-message href="#grouping">Grouping</x-message>
            </h3>
            <x-box id="grouping-buttons">
              <x-button
                id="group-button"
                value="group"
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.group()}
              >
                <x-icon href="#group"></x-icon>
                <x-label>
                  <x-message>Group</x-message>
                </x-label>
              </x-button>
              <x-button
                id="ungroup-button"
                value="ungroup"
                condensed=""
                role="button"
                aria-disabled="true"
                tabindex="-1"
                skin="default"
                onClick={() => this.unGroup()}
              >
                <x-icon href="#ungroup"></x-icon>
                <x-label>
                  <x-message>Ungroup</x-message>
                </x-label>
              </x-button>
            </x-box>
          </section>
        </main>
      </div>
    );
  };
}

export default Arrangement;
