/**
 * @name resize
 * @Autor: cbb
 * @Date: 2022-12-11 17:16:03
 * @LastEditTime: 2022-12-12 10:14:03
 */
import Draggable from '../dependencies/draggable';
import Matrix from '../dependencies/matrix';
import { Stage } from '..';

class Resize {
  #stage: Stage;
  #isResizing: Boolean = false;
  #isMoving: Boolean = false;
  #isDisabled: Boolean = false;
  #scale = 1;
  #cacheX = 0;
  #cacheY = 0;
  #cacheWidth = 0;
  #cacheHeight = 0;
  #temMatrix?: SVGMatrix;
  #temBBox?: SVGRect;
  constructor(stage: Stage) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
    this.#install();
  }
  #install = () => {
    const transformHud = document.querySelector('#transform-hud');
    const grippie = transformHud?.querySelectorAll('.grippie');
    grippie?.forEach((node: any) => {
      new Draggable().install({
        element: node,
        start: this.#start,
        move: this.#move,
        end: this.#end,
        un_shiftKey: true,
      });
    });
  };
  #start = (ev: MouseEvent) => {
    if (ev.button !== 0 || this.#isDisabled || this.#stage.select?.isDragging()) return;
    this.#isResizing = true;
    const frame = this.#stage.select?.frame() as SVGRectElement;
    this.#temMatrix = frame.getMatrix() as SVGMatrix;
    this.#temBBox = frame.getBBox() as SVGRect;
    const eleBox = frame.getBoundingClientRect();
    const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
    this.#scale = this.#stage.matrix.a;
    this.#cacheX = (eleBox.x - left - this.#stage.matrix.e) / this.#scale;
    this.#cacheY = (eleBox.y - top - this.#stage.matrix.f) / this.#scale;
    this.#cacheWidth = eleBox.width / this.#scale;
    this.#cacheHeight = eleBox.height / this.#scale;

    this.#stage.select?.get().forEach((item: any) => {
      const node = item.node;
      const matrix = node.getMatrix() as SVGMatrix;
      const box = node.getBoundingClientRect();
      item.matrixIterator = node.parentElement.getMatrixIterator();
      const { a, d, e, f } = item.matrixIterator;
      item.x = (box.x - left - e) / a;
      item.y = (box.y - top - f) / d;
      item.w = box.width / a;
      item.h = box.height / d;
      item.disX = (box.x - eleBox.x) / matrix.a;
      item.disY = (box.y - eleBox.y) / matrix.d;
      item.disR = (box.right - eleBox.right) / matrix.a;
      item.disB = (box.bottom - eleBox.bottom) / matrix.d;
      item.matrix = matrix;
    });
  };
  #move = (ev: any) => {
    if (!this.#isResizing) return;
    const { dataX, dataY, shiftKey } = ev;
    if (!this.#isMoving && ev instanceof Draggable) {
      this.#stage.history?.add(this.#stage.history.actions().MOUSE_SCALE, '缩放');
      if (dataX !== 0 || dataY !== 0) this.#isMoving = true;
    }
    const scale = ev instanceof Draggable ? this.#scale : this.#stage.matrix.a;
    const frame = this.#stage.select?.frame() as SVGRectElement;
    const eleBox = frame.getBoundingClientRect() as DOMRect;

    if (!(ev instanceof Draggable)) {
      this.#temMatrix = frame.getMatrix() as SVGMatrix;
      this.#temBBox = frame.getBBox() as SVGRect;
    }

    const pos = ev instanceof Draggable ? ev.element.getAttribute('data-position') : ev.pos;

    let a = 1,
      b = 0,
      c = 0,
      d = 1,
      e = 0,
      f = 0;
    const { x, y, width, height } = this.#temBBox as SVGRect;
    switch (pos) {
      case 'top':
        d = 1 - dataY / scale / height;
        f = (1 - d) * (y + height);
        break;
      case 'left':
        a = 1 - dataX / scale / width;
        e = (1 - a) * (x + width);
        break;
      case 'bottom':
        d = 1 + dataY / scale / height;
        f = (1 - d) * y;
        break;
      case 'right':
        a = 1 + dataX / scale / width;
        e = (1 - a) * x;
        break;
      case 'top-left':
        {
          const equal = Math.max(dataX / scale / width, dataY / scale / height);
          a = 1 - (shiftKey ? equal : dataX / scale / width);
          d = 1 - (shiftKey ? equal : dataY / scale / height);
          e = (1 - a) * (x + width);
          f = (1 - d) * (y + height);
        }
        break;
      case 'top-right':
        {
          const equal = Math.min(1 + dataX / scale / width, 1 - dataY / scale / height);
          a = shiftKey ? equal : 1 + dataX / scale / width;
          d = shiftKey ? equal : 1 - dataY / scale / height;
          e = (1 - a) * x;
          f = (1 - d) * (y + height);
        }
        break;
      case 'bottom-left':
        {
          const equal = Math.min(1 - dataX / scale / width, 1 + dataY / scale / height);
          a = shiftKey ? equal : 1 - dataX / scale / width;
          d = shiftKey ? equal : 1 + dataY / scale / height;
          e = (1 - a) * (x + width);
          f = (1 - d) * y;
        }
        break;
      case 'bottom-right':
        {
          const equal = Math.min(dataX / scale / width, dataY / scale / height);
          a = 1 + (shiftKey ? equal : dataX / scale / width);
          d = 1 + (shiftKey ? equal : dataY / scale / height);
          e = (1 - a) * x;
          f = (1 - d) * y;
        }
        break;
    }

    frame.setAttribute('transform', `matrix(${a},${b},${c},${d},${e},${f})`);

    this.#stage.select?.get().forEach((item: any) => {
      const node = item.node;
      const strokeW = node.getAttribute('stroke-width') || 1;
      if (strokeW > 0 && node.localName !== 'g') {
        node.style.vectorEffect = 'non-scaling-stroke';
        node.style.strokeWidth = strokeW * this.#stage.matrix.a;
      }

      if (ev?.pos) {
        const m = new Matrix();
        const matrix = node.getMatrix() as SVGMatrix;
        const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
        const box = node.getBoundingClientRect() as DOMRect;
        const CTM = node.getCTM() as SVGMatrix;
        item.x = (box.x - left - m.getTrans(node, 'sx')) / CTM.a;
        item.y = (box.y - top - m.getTrans(node, 'sy')) / CTM.d;
        item.w = box.width / CTM.a;
        item.h = box.height / CTM.d;
        item.disX = (box.x - eleBox.x) / matrix.a;
        item.disY = (box.y - eleBox.y) / matrix.d;
        item.disR = (box.right - eleBox.right) / matrix.a;
        item.disB = (box.bottom - eleBox.bottom) / matrix.d;
        item.matrix = matrix;
      }
      const m = new Matrix();
      m.add(
        item.matrix.a,
        item.matrix.b,
        item.matrix.c,
        item.matrix.d,
        item.matrix.e,
        item.matrix.f,
      );

      const xy = (): any => {
        switch (pos) {
          case 'top':
            return {
              x: 0,
              y: (item.y + item.h - item.matrix.f) / item.matrix.d - item.disB / scale,
            };
          case 'left':
            return {
              x: (item.x + item.w - item.matrix.e) / item.matrix.a - item.disR / scale,
              y: 0,
            };
          case 'bottom':
            return {
              x: 0,
              y: (item.y - item.matrix.f) / item.matrix.d - item.disY / scale,
            };
          case 'right':
            return {
              x: (item.x - item.matrix.e) / item.matrix.a - item.disX / scale,
              y: 0,
            };
          case 'top-left':
            return {
              x: (item.x + item.w - item.matrix.e) / item.matrix.a - item.disR / scale,
              y: (item.y + item.h - item.matrix.f) / item.matrix.d - item.disB / scale,
            };
          case 'top-right':
            return {
              x: (item.x - item.matrix.e) / item.matrix.a - item.disX / scale,
              y: (item.y + item.h - item.matrix.f) / item.matrix.d - item.disB / scale,
            };

          case 'bottom-left':
            return {
              x: (item.x + item.w - item.matrix.e) / item.matrix.a - item.disR / scale,
              y: (item.y - item.matrix.f) / item.matrix.d - item.disY / scale,
            };

          case 'bottom-right':
            return {
              x: (item.x - item.matrix.e) / item.matrix.a - item.disX / scale,
              y: (item.y - item.matrix.f) / item.matrix.d - item.disY / scale,
            };
        }
      };

      m.scale(a, d, Number(xy().x), Number(xy().y));

      const na = m.a.toFixed(9);
      const nb = m.b.toFixed(9);
      const nc = m.c.toFixed(9);
      const nd = m.d.toFixed(9);
      const ne = m.e.toFixed(9);
      const nf = m.f.toFixed(9);
      item.node.setAttribute('transform', `matrix(${na},${nb},${nc},${nd},${ne},${nf})`);
    });
    this.#stage.select?.resetFrame(false);
    if (ev?.pos) {
      this.#isResizing = true;
      this.#end();
    }
  };
  #end = () => {
    if (!this.#isResizing) return;
    this.#stage.select?.frame()?.removeAttribute('transform');
    this.#stage.select?.resetFrame(true);
    setTimeout(() => {
      this.#isResizing = false;
      this.#isMoving = false;
    });
  };
  resizeWH = (ev: any) => {
    this.#isResizing = true;
    this.#move(ev);
  };
  isResizing = () => {
    return this.#isResizing;
  };
  disabled = (isDisabled: boolean) => {
    this.#isDisabled = isDisabled;
  };
}
export default Resize;
