import "./index.css";
import React from "react";
import { Stage } from "../../../lux";

interface State {
  fonts: Array<any>;
}

interface Props {}

class Typography extends React.Component {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      fonts: [],
    };
  }

  changeAttribute = (ev: any, type: string) => {
    const stage = (window as any).stage as Stage;
    const { target } = ev;
    const toggled = target.toggled;
    const textAnchorButtons = document.querySelector("#text-anchor-buttons");
    const tAbtns = textAnchorButtons?.querySelectorAll("x-button");

    stage.select?.checkedsTexts().forEach((text: SVGTextElement) => {
      switch (type) {
        case "bold":
          if (!toggled) {
            text.setAttribute("font-weight", "700");
            target.setAttribute("toggled", "");
          } else {
            text.removeAttribute("font-weight");
            target.removeAttribute("toggled");
          }
          break;
        case "italic":
          if (!toggled) {
            text.setAttribute("font-style", "italic");
            target.setAttribute("toggled", "");
          } else {
            text.removeAttribute("font-style");
            target.removeAttribute("toggled");
          }
          break;
        case "start":
          tAbtns?.forEach((b) => b.removeAttribute("toggled"));
          text.setAttribute("text-anchor", "start");
          target.setAttribute("toggled", "");
          break;
        case "middle":
          tAbtns?.forEach((b) => b.removeAttribute("toggled"));
          text.setAttribute("text-anchor", "middle");
          target.setAttribute("toggled", "");
          break;
        case "end":
          tAbtns?.forEach((b) => b.removeAttribute("toggled"));
          text.setAttribute("text-anchor", "end");
          target.setAttribute("toggled", "");
          break;
      }
    });
    stage.select?.resetFrame(true);
  };

  letterChange = (ev: any) => {
    const stage = (window as any).stage as Stage;
    const letterSlider = document.querySelector("#letter-spacing-slider");
    const letterInput = document.querySelector("#letter-spacing-input");
    const { target } = ev;
    letterSlider?.setAttribute("value", target.value);
    letterInput?.setAttribute("value", target.value);
    stage.select?.checkedsTexts().forEach((text: SVGTextElement) => {
      text.setAttribute("letter-spacing", target.value);
    });
    stage.select?.resetFrame(true);
  };

  lineChange = (ev: any) => {
    const stage = (window as any).stage as Stage;
    const lineSlider = document.querySelector("#line-spacing-slider");
    const lineInput = document.querySelector("#line-spacing-input");
    const { target } = ev;
    lineSlider?.setAttribute("value", target.value);
    lineInput?.setAttribute("value", target.value);
    stage.select?.checkedsTexts().forEach((text: SVGTextElement) => {
      const tspan = text.querySelectorAll("tspan");
      tspan.forEach((node: Element) => {
        node.setAttribute("dy", target.value + "em");
      });
    });
    stage.select?.resetFrame(true);
  };

  fontSizeChange = (ev: any) => {
    const stage = (window as any).stage as Stage;
    const fontSizeSlider = document.querySelector("#font-size-slider");
    const fontSideInput = document.querySelector("#font-size-input");
    const { target } = ev;
    fontSizeSlider?.setAttribute("value", target.value);
    fontSideInput?.setAttribute("value", target.value);
    stage.select?.checkedsTexts().forEach((text: SVGTextElement) => {
      text.setAttribute("font-size", target.value);
    });
    stage.select?.resetFrame(true);
  };

  queryLocalFonts = async () => {
    const filterList = [] as Array<string>;
    const fontList = [] as Array<any>;
    const fonts = await (window as any).queryLocalFonts();
    fonts.forEach((item: any) => {
      if (filterList.includes(item.family)) return;
      filterList.push(item.family);
      fontList.push(item);
    });
    this.setState({
      fonts: fontList,
    });
  };

  fontChange = (ev: any) => {
    const family = ev.detail.newValue;
    const stage = (window as any).stage as Stage;
    stage.select?.checkedsTexts().forEach((text: SVGTextElement) => {
      text.setAttribute("font-family", family);
    });
    stage.select?.resetFrame(true);
  };

  init = () => {
    const fontSizeSlider = document.querySelector("#font-size-slider");
    const fontSideInput = document.querySelector("#font-size-input");
    fontSizeSlider?.addEventListener("changeend", this.fontSizeChange);
    fontSideInput?.addEventListener("changeend", this.fontSizeChange);
    fontSideInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });
    const letterSlider = document.querySelector("#letter-spacing-slider");
    const letterInput = document.querySelector("#letter-spacing-input");
    letterSlider?.addEventListener("changeend", this.letterChange);
    letterInput?.addEventListener("changeend", this.letterChange);
    letterInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });
    const lineSlider = document.querySelector("#line-spacing-slider");
    const lineInput = document.querySelector("#line-spacing-input");
    lineSlider?.addEventListener("changeend", this.lineChange);
    lineInput?.addEventListener("changeend", this.lineChange);
    lineInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });

    const fontSelect = document.querySelector("#font-select");
    fontSelect?.addEventListener("change", this.fontChange);
  };

  watch = () => {
    const stage = (window as any).stage as Stage;

    const bold = document.querySelector("#bold");
    const italic = document.querySelector("#italic");

    const start = document.querySelector("#start-text-anchor-button");
    const middle = document.querySelector("#middle-text-anchor-button");
    const end = document.querySelector("#end-text-anchor-button");

    const fontSizeSlider = document.querySelector("#font-size-slider");
    const fontSideInput = document.querySelector("#font-size-input");
    const letterSlider = document.querySelector("#letter-spacing-slider");
    const letterInput = document.querySelector("#letter-spacing-input");
    const lineSlider = document.querySelector("#line-spacing-slider");
    const lineInput = document.querySelector("#line-spacing-input");
    const fontSelect = document.querySelector("#font-select");

    stage.watch(stage.actions().SELECTED, () => {
      fontSizeSlider?.setAttribute("value", "0");
      fontSideInput?.setAttribute("value", "0");
      letterSlider?.setAttribute("value", "0");
      letterInput?.setAttribute("value", "0");
      lineSlider?.setAttribute("value", "0");
      lineInput?.setAttribute("value", "0");
      bold?.removeAttribute("toggled");
      italic?.removeAttribute("toggled");
      start?.removeAttribute("toggled");
      middle?.removeAttribute("toggled");
      end?.removeAttribute("toggled");

      fontSizeSlider?.removeAttribute("disabled");
      fontSideInput?.removeAttribute("disabled");
      letterSlider?.removeAttribute("disabled");
      letterInput?.removeAttribute("disabled");
      lineSlider?.removeAttribute("disabled");
      lineInput?.removeAttribute("disabled");
      bold?.removeAttribute("disabled");
      italic?.removeAttribute("disabled");
      start?.removeAttribute("disabled");
      middle?.removeAttribute("disabled");
      end?.removeAttribute("disabled");

      const fT = stage.select?.checkedsTexts().keys().next().value;

      if (fT) {
        const fontWeight = window.getComputedStyle(fT).fontWeight;
        const fontStyle = window.getComputedStyle(fT).fontStyle;
        const fontSize = parseFloat(window.getComputedStyle(fT).fontSize);
        const textAnchor = window.getComputedStyle(fT).textAnchor;
        const fontFamily = window.getComputedStyle(fT).fontFamily;
        const letterSpacing =
          window.getComputedStyle(fT).letterSpacing === "normal"
            ? "0"
            : parseFloat(window.getComputedStyle(fT).letterSpacing).toString();
        fontSizeSlider?.setAttribute("value", fontSize.toString());
        fontSideInput?.setAttribute("value", fontSize.toString());
        letterSlider?.setAttribute("value", letterSpacing);
        letterInput?.setAttribute("value", letterSpacing);
        const family = fontFamily
          .split(",")
          .pop()
          ?.toString()
          .replace(/\"/gim, "");
        const menuitem = fontSelect?.querySelectorAll("x-menuitem");

        menuitem?.forEach((menuitem) => {
          menuitem.removeAttribute("toggled");
          if (menuitem.getAttribute("value") === family?.trim()) {
            menuitem.setAttribute("toggled", "");
          }
        });

        switch (textAnchor) {
          case "start":
            start?.setAttribute("toggled", "");
            break;
          case "middle":
            middle?.setAttribute("toggled", "");
            break;
          case "end":
            end?.setAttribute("toggled", "");
            break;
        }

        if (parseFloat(fontWeight) === 700) bold?.setAttribute("toggled", "");
        if (fontStyle === "italic") italic?.setAttribute("toggled", "");

        try {
          stage.select?.checkedsTexts().forEach((t: SVGTextElement) => {
            if (t.querySelector("tspan")) {
              const lH = t.querySelector("tspan")?.getAttribute("dy");
              lineSlider?.setAttribute(
                "value",
                parseFloat(lH || "1").toString()
              );
              lineInput?.setAttribute(
                "value",
                parseFloat(lH || "1").toString()
              );

              throw new Error("exit");
            }
          });
        } catch (error) {}
      } else {
        fontSizeSlider?.setAttribute("disabled", "");
        fontSideInput?.setAttribute("disabled", "");
        letterSlider?.setAttribute("disabled", "");
        letterInput?.setAttribute("disabled", "");
        lineSlider?.setAttribute("disabled", "");
        lineInput?.setAttribute("disabled", "");
        bold?.setAttribute("disabled", "");
        italic?.setAttribute("disabled", "");
        start?.setAttribute("disabled", "");
        middle?.setAttribute("disabled", "");
        end?.setAttribute("disabled", "");
      }
    });
  };

  componentDidMount() {
    this.init();
    this.watch();
    this.queryLocalFonts();
  }
  componentWillUnmount() {}

  render = () => {
    return (
      <div id="typography">
        <main>
          <h2>
            <x-message>typography</x-message>
          </h2>
          <section id="font-section">
            <h3 id="font-section-heading">
              <x-message>Font</x-message>
            </h3>
            <x-box id="font-family-subsection" vertical="">
              <h4 id="font-family-heading">
                <x-message>Family</x-message>
              </h4>

              <x-select id="font-select" onClick={() => this.queryLocalFonts()}>
                <x-menu id="menu-wrap">
                  {this.state.fonts.map((item, index) => (
                    <x-menuitem key={index} value={item.family}>
                      <x-label>{item.family}</x-label>
                    </x-menuitem>
                  ))}
                </x-menu>
              </x-select>
            </x-box>
            <x-box id="font-face-subsection" vertical="">
              <h4 id="font-face-heading">
                <x-message>Face</x-message>
              </h4>
              <x-box id="font-face-box">
                <x-buttons>
                  <x-button
                    id="bold"
                    onClick={(ev: any) => this.changeAttribute(ev, "bold")}
                  >
                    Bold
                  </x-button>
                  <x-button
                    id="italic"
                    onClick={(ev: any) => this.changeAttribute(ev, "italic")}
                  >
                    Italic
                  </x-button>
                </x-buttons>
              </x-box>
            </x-box>
            <x-box id="font-size-subsection" vertical="">
              <h4 id="font-size-heading">
                <x-message>Size</x-message>
              </h4>
              <x-box id="font-size-box">
                <x-slider
                  id="font-size-slider"
                  value="26"
                  min="1"
                  max="150"
                  step="1"
                  aria-disabled="false"
                  tabindex="0"
                ></x-slider>
                <x-numberinput
                  id="font-size-input"
                  value="26"
                  min="1"
                  step="1"
                  condensed=""
                  role="input"
                  aria-disabled="true"
                  tabindex="-1"
                  hasstepper=""
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </x-box>
          </section>
          <hr />
          <section id="text-section">
            <h3 id="text-section-heading" disabled="">
              <x-message href="#text">Text</x-message>
            </h3>
            {/* <x-box id="text-transform-box" vertical="">
              <h4 id="text-transform-heading" disabled="">
                <x-message href="#transform">Transform</x-message>
              </h4>
              <x-buttons id="text-transform-buttons">
                <x-button
                  id="capitalize-button"
                  value="capitalize"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-transform.capitalize">
                      Capitalize
                    </x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="uppercase-button"
                  value="uppercase"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-transform.uppercase">
                      Uppercase
                    </x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="lowercase-button"
                  value="lowercase"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  disabled=""
                >
                  <x-label>
                    <x-message href="#text-transform.lowercase">
                      Lowercase
                    </x-message>
                  </x-label>
                </x-button>
              </x-buttons>
            </x-box> */}
            <x-box id="text-anchor-box" vertical="">
              <h4 id="text-anchor-heading" disabled="">
                <x-message href="#anchor">Anchor</x-message>
              </h4>
              <x-buttons id="text-anchor-buttons">
                <x-button
                  id="start-text-anchor-button"
                  value="start"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  onClick={(ev: any) => this.changeAttribute(ev, "start")}
                >
                  <x-label>
                    <x-message>Start</x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="middle-text-anchor-button"
                  value="middle"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  onClick={(ev: any) => this.changeAttribute(ev, "middle")}
                >
                  <x-label>
                    <x-message href="#text-anchor.middle">Middle</x-message>
                  </x-label>
                </x-button>
                <x-button
                  id="end-text-anchor-button"
                  value="end"
                  role="button"
                  aria-disabled="true"
                  tabindex="-1"
                  skin="default"
                  onClick={(ev: any) => this.changeAttribute(ev, "end")}
                >
                  <x-label>
                    <x-message>End</x-message>
                  </x-label>
                </x-button>
              </x-buttons>
            </x-box>
          </section>
          <hr />
          <section id="spacing-section">
            <h3 id="spacing-section-heading">
              <x-message href="#spacing">Spacing</x-message>
            </h3>
            <x-box id="letter-spacing-box" vertical="">
              <h4 id="letter-spacing-heading">
                <x-message>Letter</x-message>
              </h4>
              <x-box id="letter-spacing-inner-box">
                <x-slider
                  id="letter-spacing-slider"
                  value="0"
                  min="-20"
                  max="20"
                  step="0.1"
                  aria-disabled="false"
                  tabindex="0"
                ></x-slider>
                <x-numberinput
                  id="letter-spacing-input"
                  value="0"
                  step="0.1"
                  condensed=""
                  role="input"
                  aria-disabled="true"
                  tabindex="-1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </x-box>
            <x-box id="line-spacing-box" vertical="">
              <h4 id="line-spacing-heading">
                <x-message>Line</x-message>
              </h4>
              <x-box id="line-spacing-inner-box">
                <x-slider
                  id="line-spacing-slider"
                  value="0"
                  min="-3"
                  max="3"
                  step="0.1"
                  aria-disabled="false"
                  tabindex="0"
                ></x-slider>
                <x-numberinput
                  id="line-spacing-input"
                  value="0"
                  step="0.1"
                  condensed=""
                  role="input"
                  aria-disabled="true"
                  tabindex="-1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </x-box>
          </section>
        </main>
      </div>
    );
  };
}

export default Typography;
