import { Stage } from '..';
class Hover {
  #stage: Stage;
  #outline?: SVGElement;
  constructor(stage: Stage) {
    if (!stage) throw 'stage not found!';
    this.#stage = stage;
    this.#install();
  }
  #install = () => {
    this.#outline = document.querySelector("#outline-hud [uid='outline']") as SVGElement;
    this.#stage.stage?.addEventListener('mousemove', this.#mousemove);
  };
  #mousemove = (ev: any) => {
    if (this.#stage.select?.isDragging() || this.#stage.resize?.isResizing()) return;
    let node: SVGElement | undefined | null;
    if (this.#stage.currentType === this.#stage.tools().SELECT) {
      const paths = ev.path || ev.composedPath();
      const len = this.#stage.layer?.children?.length;
      for (let i = 0; i < len; i++) {
        const len = paths.length;
        for (let j = 0; j < len; j++) {
          if (paths[j] === this.#stage.layer?.children[i]) node = paths[j];
        }
      }
    } else if (
      (this.#stage.currentType === this.#stage.tools().ASELECT && ev.target?.localName !== 'g') ||
      this.#stage.currentType === this.#stage.tools().TEXT
    ) {
      node = ev.target;
      if (node?.localName !== 'text' && this.#stage.currentType === this.#stage.tools().TEXT)
        node = null;
    }

    if (node instanceof SVGElement) {
      if (window.getComputedStyle(node).pointerEvents === 'none') return;
      if (this.#stage.select?.has(node)) return;
      if (!this.#stage.svgRoot?.contains(node)) return;
      this.highLight(node);
    }
  };
  highLight = (node: any) => {
    if (!node) return;
    let D = '';
    const { x, y, width, height } = node.getBBox();
    this.#outline?.removeAttribute('transform');
    switch (node.localName) {
      case 'path':
        D = node.getAttribute('d');
        break;
      case 'ellipse':
      case 'circle':
        const cx = x + width / 2;
        const cy = y + height / 2;
        const rx = width / 2;
        const ry = height / 2;
        D = `M ${cx - rx} ${cy}
             A ${rx} ${ry} 0 0 1 ${cx + rx} ${cy}
             A ${rx} ${ry} 0 0 1 ${cx} ${cy + ry}
             A ${rx} ${ry} 0 0 1 ${cx - rx} ${cy}
         Z`;
        break;
      case 'line':
        const x1 = node.getAttribute('x1');
        const y1 = node.getAttribute('y1');
        const x2 = node.getAttribute('x2');
        const y2 = node.getAttribute('y2');
        D = `M ${x1} ${y1} L ${x2} ${y2} Z`;
        break;
      case 'polyline':
      case 'polygon':
        for (let t = 0; t < node.points.numberOfItems; t += 1) {
          const s = node.points.getItem(t);
          D += `${0 === t ? 'M' : 'L'} ${s.x} ${s.y} `;
        }
        D += 'Z';
        break;
      default:
        D = `M ${x} ${y}
            H ${x + width} V ${y + height}
            H ${x} V ${y}
          Z`;
    }
    const CTM = node.getCTM();
    const cCTM = (this.#stage.canvas as SVGAElement)?.getCTM() as SVGMatrix;
    this.#outline?.setAttribute(
      'transform',
      `matrix(
      ${CTM.a / cCTM.a},
      ${CTM.b / cCTM.a},
      ${CTM.c / cCTM.d},
      ${CTM.d / cCTM.d},
      ${(CTM.e - cCTM.e) / cCTM.a},
      ${(CTM.f - cCTM.f) / cCTM.d})`,
    );

    this.#outline?.setAttribute('d', D);
    node.onmouseout = this.closeOutline;
  };
  closeOutline = () => {
    this.#outline?.removeAttribute('d');
  };
  disabled = (isDisabled: boolean) => {
    if (isDisabled) {
      this.uninstall();
    } else {
      this.#install();
    }
  };
  uninstall = () => {
    document.removeEventListener('mousemove', this.#mousemove);
  };
}
export default Hover;
