let currentType = "";

class Toolbar extends React.Component<Props, State> {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      stage: void 0,
      toggleOtherShape: false,
    };
  }

  selectOtherShape = (ev: MouseEvent, item:any) => { 
    const stage = (window as any).stage;
    stage.shape.plugin(item.d);
    console.log(ev)
  }

  tool = (item: any) => {
    switch (item.value) {
      case "Select":
        this.state.stage?.tool(this.state.stage.tools().SELECT);
        break;
      case "Pan":
        this.state.stage?.tool(this.state.stage.tools().PAN);
        break;
      case "Freehand":
        this.state.stage?.tool(this.state.stage.tools().FREEHAND);
        break;
      case "Rectangle":
        this.state.stage?.tool(this.state.stage.tools().RECT);
        break;
      case "Ellipse":
        this.state.stage?.tool(this.state.stage.tools().ELLIPSE);
        break;
      case "Triangle":
        this.state.stage?.tool(this.state.stage.tools().TRIANGLE);
        break;
      case "Line":
        this.state.stage?.tool(this.state.stage.tools().LINE);
        break;
      case "OtherShape":
        if (currentType === item.value) {
          this.setState({
            toggleOtherShape: !this.state.toggleOtherShape,
          });
        }
        this.state.stage?.tool(this.state.stage.tools().OTHER_SHAPE);
        break;
      case "Text":
        this.state.stage?.tool(this.state.stage.tools().TEXT);
        break;
      case "View":
        this.state.stage?.tool(this.state.stage.tools().VIEW);
        break;
    }

    currentType = item.value;
  };

  keydown = (ev: KeyboardEvent) => {
    ev.preventDefault();
    const { ctrlKey, shiftKey, code } = ev;

    switch (code) {
      case "Delete":
      case "Backspace":
        this.state.stage?.select?.nodesRemove();
        break;
      case "KeyV":
      case "Escape":
        this.state.stage?.tool(this.state.stage.tools().SELECT);
        break;
      case "Space":
        this.state.stage?.tool(this.state.stage.tools().PAN);
        break;
      case "KeyT":
        this.state.stage?.tool(this.state.stage.tools().TEXT);
        break;
      case "KeyM":
        this.state.stage?.tool(this.state.stage.tools().RECT);
        break;
      case "KeyL":
        this.state.stage?.tool(this.state.stage.tools().ELLIPSE);
        break;
      case "Backslash":
        this.state.stage?.tool(this.state.stage.tools().LINE);
        break;
      case "ArrowUp":
        this.state.stage?.nodeMove("up", 1);
        break;
      case "ArrowDown":
        this.state.stage?.nodeMove("down", 1);
        break;
      case "ArrowLeft":
        this.state.stage?.nodeMove("left", 1);
        break;
      case "ArrowRight":
        this.state.stage?.nodeMove("right", 1);
        break;
    }
    ctrlKey &&
      ev.key.toLowerCase() === "c" &&
      this.state.stage?.clipboard?.copy();
    ctrlKey &&
      ev.key.toLowerCase() === "x" &&
      this.state.stage?.clipboard?.cut();
    ctrlKey &&
      ev.key.toLowerCase() === "v" &&
      this.state.stage?.clipboard?.paste();
    if (ctrlKey && ev.key.toLocaleLowerCase() === "z") {
      shiftKey
        ? this.state.stage?.history?.redo()
        : this.state.stage?.history?.undo();
    }
  };

  keyup = () => {
    if (this.state.stage?.currentType === this.state.stage?.tools().PAN) {
      this.state.stage?.tool(preTool);
    }
  };

  init = () => {
    this.state.stage?.watch(
      this.state.stage.actions().TOOL,
      (action: Symbol) => {
        if (action !== this.state.stage?.tools().PAN) preTool = action;

        if (
          currentType === "OtherShape" &&
          action !== this.state.stage?.tools().OTHER_SHAPE
        ) {
          this.setState({
            toggleOtherShape: false,
          });
        }

        const toolbar = document.querySelector("#toolbar");
        const xb = toolbar?.querySelector("x-buttons");
        xb?.childNodes.forEach((node: ChildNode) => {
          (node as Element).removeAttribute("toggled");
          if (action === this.state.stage?.tools().SELECT) {
            if (node.textContent === "Select") {
              (node as Element).toggleAttribute("toggled");
            }
          } else if (action === this.state.stage?.tools().PAN) {
            if (node.textContent === "Pan") {
              (node as Element).toggleAttribute("toggled");
            }
          } else if (action === this.state.stage?.tools().FREEHAND) {
            if (node.textContent === "Freehand") {
              (node as Element).toggleAttribute("toggled");
            }
          } else if (action === this.state.stage?.tools().RECT) {
            if (node.textContent === "Rectangle") {
              (node as Element).toggleAttribute("toggled");
            }
          } else if (action === this.state.stage?.tools().ELLIPSE) {
            if (node.textContent === "Ellipse") {
              (node as Element).toggleAttribute("toggled");
            }
          } else if (action === this.state.stage?.tools().TRIANGLE) {
            if (node.textContent === "Triangle") {
              (node as Element).toggleAttribute("toggled");
            }
          } else if (action === this.state.stage?.tools().LINE) {
            if (node.textContent === "Line") {
              (node as Element).toggleAttribute("toggled");
            }
          } else if (action === this.state.stage?.tools().OTHER_SHAPE) {
            if (node.textContent === "OtherShape") {
              (node as Element).toggleAttribute("toggled");
            }
          } else if (action === this.state.stage?.tools().TEXT) {
            if (node.textContent === "Text") {
              (node as Element).toggleAttribute("toggled");
            }
          } else if (action === this.state.stage?.tools().VIEW) {
            if (node.textContent === "View") {
              (node as Element).toggleAttribute("toggled");
            }
          }
        });
      }
    );

    this.state.stage?.watch(
      this.state.stage?.actions().TEXT,
      (text: SVGTextElement, a: string) => {
        if (a === "quit")
          this.state.stage?.tool(this.state.stage.tools().SELECT);
      }
    );
    this.state.stage?.tool(this.state.stage.tools().SELECT);
  };

  chooseOtherShape = () => {
    const os = document.querySelector(".other-shape");
    const btns = os?.querySelectorAll("x-button");

    btns?.forEach((btn: ChildNode) => {
      (btn as SVGElement).onclick = () => {
        btns.forEach((ite) => {
          (ite as SVGElement).removeAttribute("toggled");
        });
        (btn as SVGElement).toggleAttribute("toggled");

        const buttons = document.querySelector("#buttons");
        buttons?.childNodes.forEach((item) => {
          if (item.textContent === "OtherShape") {
            const href =
              (btn.childNodes[0] as Element).getAttribute("href") || "";

            (item.childNodes[0] as Element).setAttribute("href", href);
          }
        });
      };
    });
  };

  componentDidMount() {
    this.setState({
      stage: (window as any).stage,
    });

    document.addEventListener("keydown", this.keydown);
    document.addEventListener("keyup", this.keyup);

    // let reader = new FileReader();
    // reader.readAsText(svg as File);

    this.chooseOtherShape();
  }

  componentDidUpdate(
    prevProps: Readonly<Props>,
    prevState: Readonly<State>,
    snapshot?: any
  ): void {
    if (!prevState.stage) this.init();
  }

  componentWillUnmount() {
    document.removeEventListener("keydown", this.keydown);
    document.removeEventListener("keyup", this.keyup);
  }

  render = () => {
    return (
      <div id="toolbar" className="flex">
        <x-buttons id="buttons" vertical="">
          {tools.map((item, index) => (
            <x-button
              key={index}
              skin="dock"
              value={item.value}
              onClick={() => this.tool(item)}
            >
              <x-icon href={item.href}></x-icon>
              {item.value === "OtherShape" && (
                <x-icon href="#arrow-expand" class="modal-icon"></x-icon>
              )}
              <x-tooltip>
                <x-message>{item.value}</x-message>
              </x-tooltip>
            </x-button>
          ))}
        </x-buttons>
        <div
          style={{
            transform: this.state.toggleOtherShape
              ? "translateX(10px)"
              : "translateX(-281px)",
          }}
          className="other-shape"
        >
          <header>
            <h2>
              <x-message>Other Shape</x-message>
            </h2>
          </header>
          <main>
            <section style={{ padding: "12px 14px" }}>
              <h3>
                <x-message>Drawing</x-message>
              </h3>
              <h4>
                <x-message>Type</x-message>
              </h4>
              {others.map((item) => {
                return (
                  <x-buttons style={{ marginTop: "-1px" }}>
                    {item.map((item) => {
                      return (
                        <x-button onClick={(ev:any)=>this.selectOtherShape(ev,item)}>
                          <x-icon href={item.href}></x-icon>
                        </x-button>
                      );
                    })}
                  </x-buttons>
                );
              })}

            </section>
          </main>
        </div>
      </div>
    );
  };
}

export default Toolbar;
