import { Stage } from '..';
class Ruler {
  #stage: Stage;
  #horizontal?: HTMLDivElement;
  #vertical?: HTMLDivElement;
  #isDragging: Boolean = false;
  #unitRate = 1;
  constructor(stage: Stage) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
    this.#install();
  }
  #install = () => {
    this.#createRulers();
    this.#watch();
    this.#horizontal = document.querySelector('#lux-horizontal-ruler') as HTMLDivElement;
    this.#vertical = document.querySelector('#lux-vertical-ruler') as HTMLDivElement;
    this.#stage.stage?.addEventListener('mousedown', this.#mousedown);
    document.addEventListener('mousemove', this.#mousemove);
  };
  #createRulers = () => {
    const parent = this.#stage.stage as HTMLDivElement;
    parent.style.position = 'relative';
    const rulers = document.createElement('div') as HTMLDivElement;
    rulers.id = 'lux-rulers';
    rulers.innerHTML = this.#style() + this.#layout();
    parent?.append(rulers);
  };
  #watch = () => {
    this.#stage.watch(this.#stage.actions().SCALE, this.#scaleCallback);
  };
  #scaleCallback = () => {
    const { a, e, f } = this.#stage.matrix;
    const h = this.#horizontal;
    const v = this.#vertical;

    let distance = 0;
    if (0 < a && a <= 0.001) {
      distance = 50000;
    } else if (0.001 < a && a <= 0.0025) {
      distance = 20000;
    } else if (0.0025 < a && a <= 0.01) {
      distance = 10000;
    } else if (0.01 < a && a <= 0.025) {
      distance = 5000;
    } else if (0.025 < a && a <= 0.05) {
      distance = 2000;
    } else if (0.05 < a && a <= 0.1) {
      distance = 1000;
    } else if (0.1 < a && a <= 0.25) {
      distance = 500;
    } else if (0.25 < a && a <= 0.5) {
      distance = 200;
    } else if (0.5 < a && a <= 1) {
      distance = 100;
    } else if (1 < a && a <= 2.5) {
      distance = 50;
    } else if (2.5 < a && a <= 5) {
      distance = 20;
    } else if (5 < a && a <= 10) {
      distance = 10;
    } else if (10 < a && a <= 25) {
      distance = 5;
    } else if (25 < a && a <= 50) {
      distance = 2;
    } else {
      distance = 1;
    }

    if (h?.childElementCount === 0) {
      for (let i = 0; i < 30; i++) {
        const div = document.createElement('div') as HTMLDivElement;
        h?.append(div);
        v?.append(div.cloneNode(true));
      }
    }

    h?.childNodes.forEach((node: ChildNode, index: number) => {
      const t = (index - Math.floor(e / (distance * a * this.#unitRate))) * distance;
      const x = e + t * a * this.#unitRate;
      (node as SVGElement).style.transform = `translateX(${x}px)`;
      (node as SVGElement).innerHTML = t.toString();
    });

    v?.childNodes.forEach((node: ChildNode, index: number) => {
      const t = (index - Math.floor(f / (distance * a * this.#unitRate))) * distance;
      const y = f + t * a * this.#unitRate;
      (node as SVGElement).style.transform = `translateY(${y}px)`;
      (node as SVGElement).innerHTML = t.toString();
    });
  };
  #mousedown = (ev: MouseEvent) => {
    const { clientX, clientY } = ev;
    const { x, y, right, bottom } = this.#horizontal?.getBoundingClientRect() as DOMRect;
    if (clientX > x && clientX < right && clientY > y && clientY < bottom) {
      ev.stopPropagation();
      console.log('里面');
    }
  };
  #mousemove = () => {};
  setUnitRate = (unitRate: number) => {
    this.#unitRate = unitRate;
  };
  uninstall = () => {
    this.#stage.removeWatch(this.#stage.actions().SCALE, this.#scaleCallback);
  };
  #style = () => {
    return `
      <style>
      #lux-rulers{
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        contain: strict;
        size: 14px;
        pointer-events: none;
      }
      #lux-horizontal-ruler {
          position: absolute;
          top: 0px;
          width: 100%;
          height: 14px;
          border-width: 0px 0px 1px;
          border-style: none none solid;
      }
      #lux-horizontal-ruler > div {
          width: 0px;
          height: 100%;
          top: 0px;
          left: 0px;
          padding: 0px 0px 0px 3px;
          border-width: 0px 0px 0px 1px;
          border-style: none none none solid;
          color: rgba(0, 0, 0, 0.8);
          border-color: rgba(0, 0, 0, 0.4);
          position: absolute;
          font-size: 9px;
          box-sizing: border-box;
          user-select: none;
          white-space: pre;
      }
      #lux-vertical-ruler {
          position: absolute;
          top: 0px;
          width: 14px;
          height: 100%;
          border-width: 0px 1px 0px 0px;
          border-style: none solid none none;
          writing-mode: vertical-lr;
      }
      #lux-vertical-ruler > div {
        width: 100%;
        height: 0px;
        top: 0px;
        left: 0px;
        padding: 3px 0px 0px;
        border-width: 1px 0px 0px;
        border-style: solid none none;
        color: rgba(0, 0, 0, 0.8);
        border-color: rgba(0, 0, 0, 0.4);
        position: absolute;
        font-size: 9px;
        box-sizing: border-box;
        user-select: none;
        white-space: pre;
    }
      #lux-corner-ruler {
          pointer-events: all;
          position: absolute;
          left: 0px;
          top: 0px;
          width: 14px;
          height: 14px;
          box-sizing: border-box;
          border-width: 0px 1px 1px 0px;
          border-style: none solid solid none;
      }
      .lux-ruler{
        background: white;
        border-color: #dcdcdc;
      }
      </style>
    `;
  };
  #layout = () => {
    return `<div id="lux-horizontal-ruler" class="lux-ruler"></div><div id="lux-vertical-ruler" class="lux-ruler"></div><div id="lux-corner-ruler" class="lux-ruler"></div>`;
  };
}
export default Ruler;
