import "./index.css";
import React from "react";
import { Stage } from "../../../lux";

let preWidth = 0;
let preHeight = 0;
let timer: any;

class Geometry extends React.Component {
  xInputChange = (ev: any) => {
    const y = document.querySelector("#y-input")?.getAttribute("value");
    const stage = (window as any).stage;
    stage.history?.add(stage.history.actions().NODE_X, "X");
    // stage.nodeDistance(ev.target.value, y);
    stage.nodeDistance(ev.target.value, y);
    stage.select?.resetFrame(true);
  };

  yInputChange = (ev: any) => {
    const x = document.querySelector("#x-input")?.getAttribute("value");
    const stage = (window as any).stage;
    stage.history?.add(stage.history.actions().NODE_Y, "Y");
    stage.nodeDistance(x, ev.target.value);
    stage.select?.resetFrame(true);
  };

  wInputChange = () => {
    const w = document.querySelector("#w-input")?.getAttribute("value");
    const h = document.querySelector("#h-input")?.getAttribute("value");
    const stage = (window as any).stage;
    const { width, height } = stage.select?.frame().getBoundingClientRect();
    const { a: scale } = stage.matrix;

    let dataX = (Number(w) - Math.round(width / scale)) * scale;
    let dataY = (Number(h) - Math.round(height / scale)) * scale;
    stage.history?.add(stage.history.actions().NODE_WIDTH, "Width");
    stage.resize?.resizeWH({
      dataX,
      dataY,
      pos: "right",
    });
  };

  hInputChange = () => {
    const w = document.querySelector("#w-input")?.getAttribute("value");
    const h = document.querySelector("#h-input")?.getAttribute("value");
    const stage = (window as any).stage;
    const { width, height } = stage.select?.frame().getBoundingClientRect();
    const { a: scale } = stage.matrix;

    let dataX = (Number(w) - Math.round(width / scale)) * scale;
    let dataY = (Number(h) - Math.round(height / scale)) * scale;
    stage.history?.add(stage.history.actions().NODE_HEIGHT, "Height");
    stage.resize?.resizeWH({
      dataX,
      dataY,
      pos: "bottom",
    });
  };

  watch = () => {
    const stage = (window as any).stage;
    clearTimeout(timer);
    timer = setTimeout(() => {
      const { x, y, width, height } = (
        stage.select.frame() as SVGElement
      ).getBoundingClientRect();
      const xInput = document.querySelector("#x-input");
      const yInput = document.querySelector("#y-input");
      const wInput = document.querySelector("#w-input");
      const hInput = document.querySelector("#h-input");
      const { a: scale } = stage.matrix;
      const bg = document.querySelector("#background-outlines");
      const bgBox = bg?.getBoundingClientRect() as DOMRect;
      let xVal = Math.round((x - bgBox.x) / scale);
      let yVal = Math.round((y - bgBox.y) / scale);
      let wVal = Math.round(width / scale);
      let hVal = Math.round(height / scale);
      xInput?.setAttribute("value", Math.round(xVal).toString());
      yInput?.setAttribute("value", Math.round(yVal).toString());
      wInput?.setAttribute("value", Math.round(wVal).toString());
      hInput?.setAttribute("value", Math.round(hVal).toString());
      preWidth = wVal;
      preHeight = hVal;

      const geometry = document.querySelector("#geometry");
      const xInputs = geometry?.querySelectorAll("x-numberinput");
      xInputs?.forEach((input) => {
        if (wVal > 0 && hVal > 0) {
          input.removeAttribute("disabled");
        } else {
          input.setAttribute("disabled", "");
        }
      });
    }, 50);
  };

  init = () => {
    const xInput = document.querySelector("#x-input");
    xInput?.addEventListener("changeend", this.xInputChange);
    xInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });
    const yInput = document.querySelector("#y-input");
    yInput?.addEventListener("changeend", this.yInputChange);
    yInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });
    const wInput = document.querySelector("#w-input");
    wInput?.addEventListener("changeend", this.wInputChange);
    wInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });
    const hInput = document.querySelector("#h-input");
    hInput?.addEventListener("changeend", this.hInputChange);
    hInput?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });

    const stage = (window as any).stage;
    stage.watch(stage.actions().FRAME_RECT, this.watch);
  };

  componentDidMount() {
    this.init();
    this.watch();
  }

  componentWillUnmount() {}

  render = () => {
    return (
      <div id="geometry">
        <main>
          <h2>
            <x-message>Geometry</x-message>
          </h2>
          <div className="geometry-content">
            <h3>
              <x-message>Object</x-message>
            </h3>
            <section id="position-section">
              <x-box id="x-box" vertical="">
                <h4 id="x-heading">
                  <x-message>X</x-message>
                </h4>
                <x-numberinput
                  id="x-input"
                  value="0"
                  condensed=""
                  role="input"
                  aria-disabled="false"
                  tabindex="0"
                  hasstepper=""
                  step="1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
              <x-box id="y-box" vertical="">
                <h4 id="y-heading">
                  <x-message>Y</x-message>
                </h4>
                <x-numberinput
                  id="y-input"
                  value="0"
                  condensed=""
                  role="input"
                  aria-disabled="false"
                  tabindex="0"
                  hasstepper=""
                  step="1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </section>
            <section id="position-section">
              <x-box id="x-box" vertical="">
                <h4 id="x-heading">
                  <x-message>width</x-message>
                </h4>
                <x-numberinput
                  id="w-input"
                  value="263.7"
                  condensed=""
                  role="input"
                  aria-disabled="false"
                  tabindex="0"
                  hasstepper=""
                  step="1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
              <x-box id="y-box" vertical="">
                <h4 id="y-heading">
                  <x-message>height</x-message>
                </h4>
                <x-numberinput
                  id="h-input"
                  value="86.2"
                  condensed=""
                  role="input"
                  aria-disabled="false"
                  tabindex="0"
                  hasstepper=""
                  step="1"
                >
                  <x-stepper></x-stepper>
                </x-numberinput>
              </x-box>
            </section>
          </div>
        </main>
      </div>
    );
  };
}

export default Geometry;
