import "./index.css";
import React from "react";
import { Stage } from "../../../lux";
import Layer from "../../../lux/support/layers";

let layers: any;

class Objects extends React.Component {
  init = () => {
    const stage = (window as any).stage as Stage;
      layers = new Layer(stage);
      layers.install("#objects-main");
  };

  componentDidMount() {
    this.init();
  }

  componentWillUnmount() {
    layers.uninstall();
  }

  render = () => {
    return (
      <div id="objects">
        <header>
          <h2>
            <x-message>Objects</x-message>
          </h2>
        </header>
        <section id="objects-content" className="flex">
            <main id="objects-main"></main>
        </section>
      </div>
    );
  };
}

export default Objects;
