/**
 * @name  Element-extensions
 * @Autor: cbb
 * @Date: 2022-09-09 16:33:41
 * @LastEditTime: 2022-12-05 16:33:41
 */

declare global {
  interface Element {
    getBox: Function;
    getAngle: Function;
    getScale: Function;
    getMatrix: Function;
    getMatrixIterator: Function;
    scale: Function;
  }
}

if (!Element.prototype.getBox) {
  Element.prototype.getBox = function () {
    const _this = this as SVGElement;
    const svgBox = _this.ownerSVGElement?.getBoundingClientRect() as DOMRect;
    const box = _this.getBoundingClientRect() as DOMRect;
    return {
      x: box.x - svgBox.x,
      right: box.right - svgBox.x,
      left: box.left - svgBox.x,
      y: box.y - svgBox.y,
      top: box.top - svgBox.y,
      bottom: box.bottom - svgBox.y,
      width: box.width,
      height: box.height,
    };
  };
}

if (!Element.prototype.getMatrix) {
  Element.prototype.getMatrix = function () {
    const _this = this as any;
    const transform = getComputedStyle(_this).transform;
    if (transform === 'none' || !transform)
      return { a: 1, b: 0, c: 0, d: 1, e: 0, f: 0, sx: 1, sy: 1, angle: 0 };
    const matrix = transform.replace(/matrix\(|\)/g, '').split(',');
    const a = +matrix[0];
    const b = +matrix[1];
    const c = +matrix[2];
    const d = +matrix[3];
    const e = +matrix[4];
    const f = +matrix[5];
    return {
      a,
      b,
      c,
      d,
      e,
      f,
      sx: Math.sqrt(a * a + b * b),
      sy: Math.sqrt(c * c + d * d),
      angle: Math.round(Math.atan2(b, a) * (180 / Math.PI)),
    };
  };
}

if (!Element.prototype.getAngle) {
  Element.prototype.getAngle = function (): Number {
    const { a, b } = this.getMatrix();
    return Math.round(Math.atan2(b, a) * (180 / Math.PI));
  };
}

if (!Element.prototype.getScale) {
  Element.prototype.getScale = function () {
    const { a, b, c, d } = this.getMatrix();
    return {
      sx: Math.sqrt(a * a + b * b),
      sy: Math.sqrt(c * c + d * d),
    };
  };
}

if (!Element.prototype.getMatrixIterator) {
  Element.prototype.getMatrixIterator = function () {
    const result = this.getMatrix();
    const parent = this.parentNode as Element;
    if (parent && parent.nodeName !== 'svg') {
      const Iterator = parent.getMatrixIterator();
      result.e = result.e * Iterator?.a;
      result.f = result.f * Iterator?.d;
      result.a *= Iterator?.a;
      result.d *= Iterator?.d;
      result.e += Iterator?.e;
      result.f += Iterator?.f;
      result.sx *= Iterator.sx;
      result.sy *= Iterator.sy;
      result.angle += Iterator.angle;
    }
    return result;
  };
}

if (!Element.prototype.scale) {
  Element.prototype.scale = function (sx: number, sy: number, cx: number, cy: number) {
    const _this = this as SVGAElement;
    const m = _this.getMatrix();
    const a = sx * m.a;
    const d = sy * m.d;
    const e = cx - a * cx;
    const f = cy - d * cy;

    _this.setAttribute(
      'transform',
      `matrix(
      ${a},
      0,
      0,
      ${d},
      ${e},
      ${f}
    )`,
    );
  };
}
export {};
