import "./App.css";
import "./global";
import Toolbar from "./views/toolbar";
import Menus from "./views/menus";
import Actionbar from "./views/actionbar";
import Contextmenu from "./views/contextmenu";
import React from "react";
import { Stage } from "./lux";

interface State {
  stage: Stage | undefined;
}

interface Props {}

class App extends React.Component<Props, State> {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      stage: void 0,
    };
  }

  componentDidMount() {
    const stage = ((window as any).stage = new Stage("board", 1980, 1200));

    setTimeout(() => {
      const board = document.querySelector("#board");
      const box = board?.getBoundingClientRect();
      let height = 600;
      let width = 600;
      try {
        width = Number(box?.height || 680) - 80;
        height = Number(box?.height || 680) - 80;
      } catch (error) {}
      stage.setViewSize(width, height);
      stage.setBoardSize(1, 20, 20);
    }, 100);

    this.setState({
      stage,
    });
  }

  componentWillUnmount() {}

  render = () => {
    return (
      <div className="App">
        {this.state.stage && <Menus />}
        <div id="editor" className="flex">
          {this.state.stage && <Toolbar />}
          <div id="board" className="svg"></div>
          {this.state.stage && <Contextmenu />}
          {this.state.stage && <Actionbar />}
        </div>
      </div>
    );
  };
}

export default App;
