class Matrix {
  a = 1;
  b = 0;
  c = 0;
  d = 1;
  e = 0;
  f = 0;
  element?: HTMLElement;
  constructor(element?: HTMLElement) {
    this.element = element as HTMLElement;
  }

  add(a: number, b: number, c: number, d: number, e: number, f: number) {
    const aNew = a * this.a + b * this.c;
    const bNew = a * this.b + b * this.d;
    this.e += e * this.a + f * this.c;
    this.f += e * this.b + f * this.d;
    this.c = c * this.a + d * this.c;
    this.d = c * this.b + d * this.d;

    this.a = aNew;
    this.b = bNew;
    return this;
  }

  translate(x: number, y: number) {
    this.e += x * this.a + y * this.c;
    this.f += x * this.b + y * this.d;
    return this;
  }

  scale(x: number, y: number, cx: number, cy: number) {
    y == null && (y = x);
    (cx || cy) && this.translate(cx, cy);
    this.a *= x;
    this.b *= x;
    this.c *= y;
    this.d *= y;
    (cx || cy) && this.translate(-cx, -cy);
    return this;
  }

  rotate(a: number, x?: number, y?: number) {
    a = this.rad(a);
    x = x || 0;
    y = y || 0;
    const cos = +Math.cos(a).toFixed(9),
      sin = +Math.sin(a).toFixed(9);
    this.add(cos, sin, -sin, cos, x, y);
    return this.add(1, 0, 0, 1, -x, -y);
  }

  skewX(a: number) {
    return this.skew(a, 0);
  }

  skewY(a: number) {
    return this.skew(0, a);
  }

  skew(b: number, c: number) {
    // (b = b || 0), (c = c || 0), (b = this.rad(b)), (c = this.rad(c));
    // const d = Math.tan(b).toFixed(9),
    //   e = Math.tan(c).toFixed(9);
    // return this.add(1, +e, +d, 1, 0, 0);
  }

  rad(deg: number) {
    return ((deg % 360) * Math.PI) / 180;
  }

  deg(rad: number) {
    return ((rad * 180) / Math.PI) % 360;
  }

  getMatrix(element: any) {
    element = this.element || element;
    const transform = window.getComputedStyle(element).transform;
    if (transform === 'none') return { a: 1, b: 0, c: 0, d: 1, e: 0, f: 0 };
    const matrix = transform.replace(/matrix\(|\)/g, '');
    const m = matrix.search(',') === -1 ? matrix.split(' ') : matrix.split(',');
    return {
      a: +m[0],
      b: +m[1],
      c: +m[2],
      d: +m[3],
      e: +m[4],
      f: +m[5],
    };
  }

  getScale(el: any, type: string) {
    let result;
    result = this.getMatrix(el.parentElement)[type === 'sx' ? 'a' : 'd'];
    if (el?.parentElement?.tagName != 'svg') result *= this.getScale(el.parentElement, type);
    return result;
  }

  getTrans(el: any, type: string) {
    let result;
    result =
      this.getMatrix(el.parentElement)[type === 'sx' ? 'e' : 'f'] *
      this.getParentScale(el.parentElement, type);

    if (el.parentElement.tagName != 'svg') result += this.getTrans(el.parentElement, type);
    return result;
  }

  getParentScale(el: any, type: string) {
    if (el.parentElement.tagName != 'g') return 1;
    let result;
    result = this.getMatrix(el.parentElement)[type === 'sx' ? 'a' : 'd'];
    if (el.parentElement.tagName != 'svg') result *= this.getParentScale(el.parentElement, type);
    return result;
  }
}

export default Matrix;
