class Contextmenu {
  #div?: HTMLDivElement;
  #menus: Array<any> = [];
  constructor() {
    this.#install();
  }
  #install = () => {
    window.addEventListener('mousedown', this.#clear);
    window.addEventListener('blur', this.#clear);
  };
  contextmenu = (ev: MouseEvent) => {
    if (this.#menus.length === 0) return;
    const { clientX, clientY } = ev;
    this.#div = document.createElement('div') as HTMLDivElement;
    this.#div.id = 'lux-contextmenu';
    this.#div.innerHTML = style();
    this.#div.onmousedown = (ev: MouseEvent) => {
      ev.stopPropagation();
    };
    this.#menus.forEach((item: any) => {
      if (item.type === 'separator') {
        const menuItem = document.createElement('hr');
        menuItem.classList.add('lux-contextmenu-hr');
        this.#div?.append(menuItem);
      } else {
        const div = document.createElement('div') as HTMLDivElement;
        div.innerHTML = item.label;
        div.classList.add('lux-contextmenu-item');
        if (item.disabled) div.classList.add('lux-contextmenu-item-disabled');
        if (item.click instanceof Function) {
          div.onclick = (ev: MouseEvent) => {
            this.#clear();
            item.click(ev);
          };
        }
        this.#div?.append(div);
      }
    });
    document.body.append(this.#div);
    this.#div.style.cssText = `left:${clientX}px;top:${clientY}px`;

    const bodyRect = document.body.getBoundingClientRect() as DOMRect;
    const box = this.#div.getBoundingClientRect() as DOMRect;
    const dy = box.bottom - bodyRect.bottom;
    const dx = box.right - bodyRect.right;
    this.#div.style.cssText = `
    left:${dx > 0 ? clientX - dx - 10 : clientX}px;
    top:${dy > 0 ? clientY - dy - 10 : clientY}px`;
  };
  #clear = () => {
    this.#div?.remove();
    document.querySelector('#lux-contextmenu')?.remove();
  };
  menus = (menus: Array<any>) => {
    this.#menus = menus;
  };
  uninstall = () => {
    window.removeEventListener('mousedown', this.#clear);
    window.removeEventListener('blur', this.#clear);
  };
}

export default Contextmenu;

const style = () => {
  return `<style>
    #lux-contextmenu {
      padding: 3px;
      color: rgba(0,0,0,0.8);
      background: #ffffff;
      border: 1px solid #dcdcdc;
      border-radius: 12px;
      box-shadow: rgb(0 0 0 / 17%) 0px 3px 10px 0px;
      position:fixed;
      left:0;
      top:0;
      z-index:10;
    }
    .lux-contextmenu-item {
      white-space: nowrap;
      color: rgba(0,0,0,0.8);
      border-radius: 8px;
      display: flex;
      flex-flow: row;
      align-items: center;
      position: relative;
      padding: 0px 23px 0px 23px;
      min-height: 28px;
      box-sizing: border-box;
      cursor: default;
      user-select: none;
      font-size:13px;
    }
    .lux-contextmenu-item:hover {
        background:rgba(0, 0, 0, 0.08);
    }
    .lux-contextmenu-item-disabled {
      pointer-events: none;
      opacity: 0.6;
    }
    .lux-contextmenu-hr{
        min-height: 1px;
        border: none;
        width: 100%;
        height: 1px;
        margin: 4px 0px;
        box-sizing: border-box;
        background: rgba(0, 0, 0, 0.1);
        transform:scale(1,0.5);
    }
  </style>`;
};
