import { Stage } from '..';
class Text {
  #stage: Stage;
  #textHud?: SVGElement;
  #htmlInput: any;
  #text?: SVGTextElement;
  #selectRects?: Element;
  #caretLine?: SVGLineElement;
  #startRange?: Range;
  #range?: Range;
  #textListener?: MutationObserver;
  #preTime = 0;
  #blurTimer?: NodeJS.Timeout;
  #startIndex = 0;
  #endIndex = 0;
  #isSelecting: Boolean = false;
  #isDisabled: Boolean = true;
  #addCallBack?: Function;
  #editCallBack?: Function;
  #changeCallBack?: Function;
  #keydownCallBack?: Function;
  #quitCallBack?: Function;
  #WATCH_ACTIONS = {
    ADD: Symbol(),
    EDIT: Symbol(),
    CHANGE: Symbol(),
    KEYDOWN: Symbol(),
    QUIT: Symbol(),
  };
  constructor(stage: Stage) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
    this.#install();
  }
  #install = () => {
    this.#textHud = document.querySelector('#text-hud') as SVGAElement;
    this.#htmlInput = document.querySelector('[uid="html-input"]') as Element;
    this.#caretLine = document.querySelector('[uid="caret-line"]') as SVGLineElement;
    this.#selectRects = document.querySelector('[uid="selection-rects"]') as SVGElement;
    this.#htmlInput?.addEventListener('keydown', this.#keydown);
    this.#htmlInput?.addEventListener('input', this.#change);
    this.#htmlInput?.addEventListener('focus', this.#focus);
    this.#htmlInput?.addEventListener('blur', this.#blur);
    this.#stage.stage?.addEventListener('mousedown', this.#mousedown);
    document.addEventListener('mousemove', this.#mousemove);
    document.addEventListener('mouseup', this.#mouseup);
  };
  watch = (action: Symbol, func: Function) => {
    if (action === this.actions().ADD) {
      this.#addCallBack = func;
    } else if (action === this.actions().EDIT) {
      this.#editCallBack = func;
    } else if (action === this.actions().CHANGE) {
      this.#changeCallBack = func;
    } else if (action === this.actions().KEYDOWN) {
      this.#keydownCallBack = func;
    } else if (action === this.actions().QUIT) {
      this.#quitCallBack = func;
    }
  };
  add = (ev: MouseEvent) => {
    if (this.#isDisabled) return;
    this.#stage.history?.add(this.#stage.history.actions().ADD, '文本');
    setTimeout(() => {
      const { offsetX, offsetY } = ev;
      const scale = this.#stage.canvas?.getMatrix().a;

      const text = this.#createNode('text') as SVGTextElement;
      const fontSize = Number(this.#stage.options.fontSize) / scale;
      text.setAttribute('font-size', fontSize.toString());
      text.setAttribute('font-family', 'MicrosoftYaHei,Microsoft YaHei,Microsoft YaHei');
      text.style.whiteSpace = 'pre';
      this.#textListen();
      text.append(document.createTextNode(this.#stage.options.textContent?.toString() || ''));
      this.#htmlInput.innerHTML = this.#stage.options.textContent;
      this.#stage.layer.append(text);
      const CTM = this.#stage.layer.getCTM();
      const x = (offsetX - CTM.e) / scale;
      const y = (offsetY - CTM.f) / scale;
      text.setAttribute('x', x.toFixed(9));
      text.setAttribute('y', y.toFixed(9));

      if (CTM.a !== CTM.d) {
        const ma = (1 / CTM.a) * scale;
        const md = (1 / CTM.d) * scale;
        const me = -(CTM.e - CTM.e) * ma;
        const mf = -(CTM.f - CTM.f) * md;
        text.setAttribute(
          'transform',
          `matrix(
            ${ma},
            ${0},
            ${0},
            ${md},
            ${me},
            ${mf}
          )`,
        );
      }

      this.#text = text;
      this.#addCallBack && this.#addCallBack(text);
      this.#textHud?.setAttribute('inputmode', 'true');
      this.#textSelection(true);
    });
  };
  edit = (ev?: MouseEvent | null, node?: Element, isSelectRange = false) => {
    if (this.#isDisabled) return;
    if (ev) {
      ev.stopPropagation();
      const { clientX, clientY } = ev;
      this.#range = document.caretRangeFromPoint(clientX, clientY) as Range;
    }
    this.#text = node as SVGTextElement;

    this.#textListen();
    this.closeRange();
    this.#htmlInput.innerHTML = '';

    this.#text.childNodes.forEach((childNodes: Node) => {
      const node = childNodes as Element;
      if (node.localName === 'tspan') {
        const text = document.createTextNode('\n');
        this.#htmlInput.append(text);
      } else {
        const cloneNode = node.cloneNode(true);
        this.#htmlInput.append(cloneNode);
      }
    });

    this.#isSelecting = false;
    this.#startRange = this.#range as Range;

    this.#text.onmousedown = (e) => {
      if (!e) return;
      this.#isSelecting = true;
    };
    if (ev) this.#text.onmousedown(ev);

    const dbl = () => {
      if (this.#caretLine?.style.visibility === 'hidden') return;
      this.#textSelection(true, false, true);
      this.#text?.removeEventListener('dblclick', dbl);
    };
    this.#text.addEventListener('dblclick', dbl);

    this.#editCallBack && this.#editCallBack(node);

    setTimeout(() => {
      this.#textSelection(isSelectRange);
      this.#textHud?.setAttribute('inputmode', 'true');
      this.#htmlInput.focus();
    });
  };
  #mousedown = (ev: MouseEvent) => {
    if (ev.button !== 0) return;
    const currentTarget = ev.target as Element;
    // if (currentTarget.localName === "tspan")
    //   currentTarget = this.queryTspanText(currentTarget);
    currentTarget.localName === 'text' ? this?.edit(ev, currentTarget) : this?.add(ev);
  };
  #mousemove = (ev: MouseEvent) => {
    if (!this.#isSelecting) return;
    const { clientX, clientY } = ev;
    const { x, y, right, bottom } = this.#text?.getBoundingClientRect() as DOMRect;
    const posX = (() => {
      if (x < clientX && clientX < right) return clientX;
      return clientX < x ? x : right;
    })() as number;
    const posY = (() => {
      if (y < clientY && clientY < bottom) return clientY;
      return clientY < y ? y : bottom;
    })() as number;
    this.#range = document.caretRangeFromPoint(posX, posY) as Range;
    this.#textSelection(false, false);
  };
  #mouseup = () => {
    if (!this.#startRange || !this.#range || !this.#isSelecting) return;
    this.#isSelecting = false;
    if (this.#text) this.#text.onmousedown = null;
    const start = this.#startRange?.startOffset as number;
    const end = this.#range?.endOffset as number;
    let min = start;
    let max = end;
    const startNode = this.#htmlInput.childNodes[this.#startIndex] as Node;
    const endNode = this.#htmlInput.childNodes[this.#endIndex] as Node;

    if (startNode === endNode) {
      min = Math.min(start, end);
      max = Math.max(start, end);
    }

    const isReverse = startNode.compareDocumentPosition(endNode) === 2 ? true : false;
    if (isReverse) {
      min = end;
      max = start;
    }

    const range = document.createRange() as Range;
    range.setStart(isReverse ? endNode : startNode, min);
    range.setEnd(isReverse ? startNode : endNode, max);
    const selection = window.getSelection() as Selection;
    selection?.removeAllRanges();
    selection?.addRange(range);
  };
  #keydown = (ev: KeyboardEvent) => {
    ev.stopPropagation();
    this.#keydownCallBack && this.#keydownCallBack(ev);
    if (!this.#text) return;
    if (ev.ctrlKey && ev.code === 'KeyA') {
      this.#textSelection(true);
    } else if (ev.ctrlKey && ev.code === 'KeyV') {
      setTimeout(() => {
        this.#change();
      });
    }
    //esc
    if (ev.keyCode === 27) {
      this.quit();
      this.#quitCallBack && this.#quitCallBack();
    }

    //direction
    const keyCode = [37, 38, 39, 40];
    if (keyCode.includes(ev.keyCode)) {
      setTimeout(() => {
        this.#change();
      });
    }
  };
  #focus = () => {
    if (!this.#range) return;
    if (this.#selectRects?.innerHTML === '') {
      if (this.#caretLine) {
        clearTimeout(this.#blurTimer);
        this.openCaretLine();
        this.setCaretLine();
      }
    }
  };
  #blur = () => {
    this.#blurTimer = setTimeout(() => {
      this.#textHud?.removeAttribute('inputmode');
    }, 10);
  };
  #change = (inputmode = true) => {
    if (!this.#text) return;
    this.closeRange();
    const selection = window.getSelection() as Selection;
    const tspan = this.#text?.querySelector('tspan') as SVGTSpanElement;
    const dy = tspan?.getAttribute('dy');
    const x = this.#text.getAttribute('x') || 0;
    let clear = false;
    if (this.#htmlInput.innerHTML === '') {
      this.#htmlInput.innerHTML = ' ';
      clear = true;
    }

    if (this.#htmlInput.innerHTML.search('br') !== -1) {
      this.#htmlInput.childNodes.forEach((node: ChildNode) => {
        if (node.nodeType !== 3) return node.remove();
      });
    }

    const n2Tspan = this.#htmlInput?.innerHTML.replace(
      /\n/gm,
      `<tspan x="${x}" dy="${dy || '1em'}">&ZeroWidthSpace;</tspan>`,
    );

    this.#text.innerHTML = n2Tspan;
    try {
      let focusIndex = 0;
      this.#htmlInput.childNodes.forEach((node: any, index: number) => {
        if (!this.#text?.childNodes[index]?.nextSibling && node.nextSibling?.localName !== '\n') {
          if (node.nextSibling) {
            node.appendData(node.nextSibling.nodeValue);
            node.nextSibling.remove();
          }
        }
        if (node === selection.focusNode) {
          focusIndex = index;
          if (node.previousSibling?.nodeValue === '\n' && node.nodeValue === '\n') focusIndex--;
        }
      });

      const focusNode = this.#text.childNodes[focusIndex] as Element;
      const acFocusNode =
        focusNode?.localName === 'tspan' ? (focusNode.firstChild as Element) : focusNode;
      this.#range = document.createRange();
      this.#range.setStart(acFocusNode, selection.focusOffset);
      this.#range.setEnd(acFocusNode, selection.focusOffset);

      if (this.#caretLine) {
        if (inputmode) this.#textHud?.setAttribute('inputmode', 'true');
      }
      this.openCaretLine();
      this.setCaretLine();
      this.#changeCallBack && this.#changeCallBack();
      if (clear) {
        this.#textSelection(true);
      }
      this.#stage.select?.resetFrame(true);
    } catch (error) {
      console.error(error);
    }
  };
  #textSelection = (isSelectAll = true, isBlur = false, lineSelection = false) => {
    this.closeRange();
    if (isSelectAll) {
      this.closeCaretLine();
      const firstChild = this.#htmlInput.firstChild;
      const lastChild = this.#htmlInput.lastChild;
      const inputRange = document.createRange() as Range;
      inputRange.setStart(firstChild, 0);
      inputRange.setEnd(lastChild, lastChild.textContent.length);
      const selection = window.getSelection() as Selection;
      selection?.removeAllRanges();

      this.#range = this.#startRange = document.createRange() as Range;
      const textFirstChild = this.#text?.firstChild as Node;
      const textLastChild = this.#text?.lastChild as Node;
      const len = textLastChild.textContent?.length as number;
      this.#range.setStart(textFirstChild, 0);
      this.#range.setEnd(textLastChild, len);

      if (!isBlur) selection?.addRange(inputRange);
    }

    const { e, f, sx, sy } = this.#stage.canvas?.getMatrix();
    const { left, top } = this.#stage.canvas?.ownerSVGElement?.getBoundingClientRect() as DOMRect;

    const startContainer = this.#startRange?.startContainer as Node;
    const endContainer = this.#range?.endContainer as Node;
    const start = this.#startRange?.startOffset as number;
    const end = this.#range?.endOffset as number;

    let min = -1;
    let max = -1;
    let type = -1;

    this.#text?.childNodes.forEach((node: Node, i: number) => {
      if (node.nodeType !== 3) return;
      if (node === startContainer) this.#startIndex = i;
      if (node === endContainer) this.#endIndex = i;
      if (startContainer === endContainer) {
        type = 1;
        min = Math.min(start, end);
        max = Math.max(start, end);
      }
      if (startContainer.compareDocumentPosition(endContainer) === 4) {
        type = 2;
        if (node === startContainer) {
          min = start;
          max = Number(node.textContent?.length);
        }
        if (node === endContainer) {
          min = 0;
          max = end;
        }
        if (node !== startContainer && node !== endContainer) {
          min = 0;
          max = Number(node.textContent?.length);
        }
      }
      if (startContainer.compareDocumentPosition(endContainer) === 2) {
        type = 3;
        if (node === startContainer) {
          min = 0;
          max = start;
        }
        if (node === endContainer) {
          min = end;
          max = Number(node.textContent?.length);
        }
        if (node !== startContainer && node !== endContainer) {
          min = 0;
          max = Number(node.textContent?.length);
        }
      }
      if (type === 1 && node !== startContainer) return;
      if (
        (type === 2 && node.compareDocumentPosition(startContainer) === 4) ||
        (type === 2 && node.compareDocumentPosition(endContainer) === 2)
      )
        return;
      if (
        (type === 3 && node.compareDocumentPosition(startContainer) === 2) ||
        (type === 3 && node.compareDocumentPosition(endContainer) === 4)
      )
        return;
      node.textContent?.split('').forEach((item: string, index: number) => {
        if ((min <= index && index + 1 <= max) || lineSelection) {
          const { x, y, width, height } = this.#queryTextRect(node, index, index + 1);
          const rect = this.#createNode('rect') as SVGRectElement;
          const nx = (x - left - e) / sx;
          const ny = (y - top - f) / sy;
          const nw = width / sx;
          const nh = height / sy;
          rect.setAttribute('x', nx.toFixed(9));
          rect.setAttribute('y', ny.toFixed(9));
          rect.setAttribute('width', nw.toFixed(9));
          rect.setAttribute('height', nh.toFixed(9));
          this.#selectRects?.append(rect);
        }
      });
    });

    if (startContainer === endContainer && min === max && this.#isSelecting) {
      this.openCaretLine();
    } else {
      this.closeCaretLine();
    }
  };
  #textListen = () => {
    setTimeout(() => {
      if (!this.#text) return;
      this.#textListener?.disconnect();
      this.#textListener = new MutationObserver(() => {
        this.autoSelection();
      });
      if (this.#text)
        this.#textListener.observe(this.#text, {
          attributes: true,
          subtree: true,
        });
    });
  };
  autoSelection = () => {
    const curTime = new Date().getTime();
    if (curTime - this.#preTime < 20) return;
    this.#preTime = curTime;
    const inputmode = this.#textHud?.hasAttribute('inputmode') as boolean;
    if (this.#selectRects?.innerHTML !== '') {
      this.#textSelection(false, !inputmode);
    }
    if (this.#caretLine?.style.visibility === 'visible') {
      this.setCaretLine();
      this.#change(inputmode);
    }
  };
  closeRange = () => {
    if (this.#selectRects) this.#selectRects.innerHTML = '';
  };
  openCaretLine = () => {
    if (!this.#caretLine) return;
    this.#caretLine.style.visibility = 'visible';
  };
  closeCaretLine = () => {
    if (!this.#caretLine) return;
    this.#caretLine.style.visibility = 'hidden';
  };
  setCaretLine = () => {
    const { left, top } = this.#stage.canvas?.ownerSVGElement?.getBoundingClientRect() as DOMRect;
    const { x, y, height } = this.#range?.getBoundingClientRect() as DOMRect;
    const { e, f, sx, sy } = this.#stage.canvas?.getMatrix();
    const lineX = (x - left - e) / sx;
    const lineY = (y - top - f) / sy;
    this.#caretLine?.setAttribute('x1', lineX.toFixed(9));
    this.#caretLine?.setAttribute('y1', lineY.toFixed(9));
    this.#caretLine?.setAttribute('x2', lineX.toFixed(9));
    this.#caretLine?.setAttribute('y2', (lineY + height / sy).toFixed(9));
  };
  #queryTextRect = (node: Node, start: number, end: number): DOMRect => {
    const min = Math.min(start, end);
    const max = Math.max(start, end);
    const range = document.createRange();
    range.setStart(node, min);
    range.setEnd(node, max);
    return range.getBoundingClientRect();
  };
  #createNode = (type: string): SVGElement => {
    return document.createElementNS('http://www.w3.org/2000/svg', type);
  };
  quit = () => {
    this.#htmlInput.blur();
    this.closeRange();
    this.closeCaretLine();
    if (this.#text) this.#text.onmousedown = null;
  };
  actions = () => {
    return this.#WATCH_ACTIONS;
  };
  disabled = (isDisabled: boolean) => {
    this.#isDisabled = isDisabled;
  };
  uninstall = () => {
    this.#htmlInput?.removeEventListener('keydown', this.#keydown);
    this.#htmlInput?.removeEventListener('input', this.#change);
    this.#htmlInput?.removeEventListener('focus', this.#focus);
    this.#htmlInput?.removeEventListener('blur', this.#blur);
    this.#stage.stage?.removeEventListener('mousedown', this.#mousedown);
    document.removeEventListener('mousemove', this.#mousemove);
    document.removeEventListener('mouseup', this.#mouseup);
    this.#textListener && this.#textListener.disconnect();
    this.closeRange();
    this.closeCaretLine();
    this.#startRange = this.#range = void 0;
  };
}

export default Text;
