import { SVGPathData } from 'svg-pathdata';
import { Stage } from '..';

interface Options {
  start?: Function;
  done?: Function;
}

class Shape {
  #stage: Stage;
  #drawingNode?: SVGElement;
  #startX = 0;
  #startY = 0;
  #isDrawing: Boolean = false;
  #isMoving: Boolean = false;
  #isDisabled = true;
  #start?: Function;
  #done?: Function;
  #cacheWidth = 0;
  #cacheHeight = 0;
  #d?: string =
    'M 8.5 4.095 C 12.004 -5.959 25.735 4.095 8.5 17.021 C -8.736 4.095 4.995 -5.959 8.5 4.095 Z';
  constructor(stage: Stage, options?: Options) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
    this.#done = options?.done;
    this.#start = options?.start;
    this.#install();
  }
  #install = () => {
    this.#stage.stage?.addEventListener('mousedown', this.#mousedown);
    document.addEventListener('mouseup', this.#mouseup);
    document.addEventListener('mousemove', this.#mousemove);
  };
  #mousedown = (ev: MouseEvent) => {
    const { button, clientX, clientY } = ev;
    if (button !== 0 || this.#isDrawing || this.#isDisabled) return;
    this.#start && this.#start();
    this.#isDrawing = true;
    this.#startX = clientX;
    this.#startY = clientY;

    this.#stage.history?.add(this.#stage.history.actions().ADD, this.#EN2CN());
  };
  #mousemove = (ev: MouseEvent) => {
    if (!this.#isDrawing) return;
    const { clientX, clientY, shiftKey } = ev;
    if (
      Math.max(clientX, this.#startX) - Math.min(clientX, this.#startX) < 1 ||
      Math.max(clientY, this.#startY) - Math.min(clientY, this.#startY) < 1
    )
      return;
    this.#isMoving = true;

    const element = this.#drawingNode || this.#createNode();
    if (
      !this.#drawingNode &&
      this.#d &&
      this.#stage.currentType === this.#stage.tools().OTHER_SHAPE
    ) {
      element.setAttribute('d', this.#d);
      const box = element.getBoundingClientRect();
      const sx = 1 / box.width;
      const sy = 1 / box.height;
      this.#cacheWidth = box.width * sx;
      this.#cacheHeight = box.height * sy;
      const d = new SVGPathData(this.#d).matrix(sx, 0, 0, sy, 0, 0).encode();
      this.#d = d;
      element.setAttribute('d', d);
    }
    this.#drawingNode = element;

    const { left: l, top: t } = element.ownerSVGElement?.getBoundingClientRect() as DOMRect;

    const { e, a, d, f } = (element.parentElement as any).getCTM() as SVGMatrix;

    const xArr = [(this.#startX - l - e) / a, (clientX - l - e) / a];
    const yArr = [(this.#startY - t - f) / d, (clientY - t - f) / d];

    const maxX = Math.max.apply(null, xArr);
    let minX = Math.min.apply(null, xArr);
    const maxY = Math.max.apply(null, yArr);
    let minY = Math.min.apply(null, yArr);

    let width = maxX - minX;
    let height = maxY - minY;

    if (shiftKey) {
      minX = width > height && minX !== xArr[0] ? xArr[0] - height : minX;
      minY = height > width && minY !== yArr[0] ? yArr[0] - width : minY;
      width = width > height ? height : width;
      height = height > width ? width : height;
    }

    const params = {
      element,
      minX,
      minY,
      width,
      height,
      xArr,
      yArr,
    };
    this.#setAttr(ev, params);
  };
  #mouseup = () => {
    if (!this.#isDrawing) return;
    const node = this.#drawingNode;
    if (this.#isMoving && node) {
      this.#done && this.#done(node);
    }

    this.#isDrawing = false;
    this.#isMoving = false;
    this.#drawingNode = void 0;
    this.#startX = 0;
    this.#startY = 0;
  };
  #setAttr = (ev: MouseEvent, attr: any) => {
    const { element, minX, minY, width, height, xArr, yArr } = attr;
    const { shiftKey, clientX, clientY } = ev;
    if (this.#stage.currentType === this.#stage.tools().RECT) {
      element.setAttribute('x', minX);
      element.setAttribute('y', minY);
      element.setAttribute('width', width);
      element.setAttribute('height', height);
    } else if (this.#stage.currentType === this.#stage.tools().ELLIPSE) {
      element.setAttribute('cx', minX + width / 2);
      element.setAttribute('cy', minY + height / 2);
      element.setAttribute('rx', width / 2);
      element.setAttribute('ry', height / 2);
    } else if (this.#stage.currentType === this.#stage.tools().TRIANGLE) {
      const d = `M ${minX} ${minY + height} L ${minX + width} ${minY + height} L ${
        minX + width / 2
      } ${minY} Z`;
      element.setAttribute('d', d);
    } else if (this.#stage.currentType === this.#stage.tools().LINE) {
      const rad = Math.atan2(yArr[1] - yArr[0], xArr[1] - xArr[0]);
      const deg = (rad * 180) / Math.PI + 180;
      element.setAttribute('x1', xArr[0]);
      element.setAttribute('y1', yArr[0]);
      element.setAttribute(
        'x2',
        (shiftKey && deg < 315 && deg > 225) || (shiftKey && deg <= 135 && deg >= 45)
          ? xArr[0]
          : xArr[1],
      );
      element.setAttribute(
        'y2',
        (shiftKey && (deg < 45 || deg >= 315)) || (shiftKey && deg <= 225 && deg > 135)
          ? yArr[0]
          : yArr[1],
      );
    } else if (this.#stage.currentType === this.#stage.tools().OTHER_SHAPE) {
      if (!this.#d) return;

      const { left: l, top: t } = element.ownerSVGElement?.getBoundingClientRect() as DOMRect;
      const m = (element.parentElement as any).getCTM() as SVGMatrix;
      const dx = clientX - this.#startX;
      const dy = clientY - this.#startY;
      const d = new SVGPathData(this.#d)
        .matrix(
          (dx + this.#cacheWidth) / this.#cacheWidth,
          0,
          0,
          (dy + this.#cacheHeight) / this.#cacheHeight,
          (this.#startX - l - m.e) / m.a,
          (this.#startY - t - m.f) / m.a,
        )
        .encode();
      element.setAttribute('d', d);
    }
  };
  #createNode = (): SVGElement => {
    const type = (() => {
      if (this.#stage.currentType === this.#stage.tools().RECT) {
        return 'rect';
      } else if (this.#stage.currentType === this.#stage.tools().ELLIPSE) {
        return 'ellipse';
      } else if (this.#stage.currentType === this.#stage.tools().LINE) {
        return 'line';
      } else {
        return 'path';
      }
    })();
    const node = document.createElementNS('http://www.w3.org/2000/svg', type);
    this.#stage.history?.end();
    this.#stage.select?.clear();
    this.#stage.select?.set(node, { node });
    this.#stage.layer.append(node);
    node.setAttribute('fill', this.#stage.options.fill || '');
    node.setAttribute('stroke', this.#stage.options.stroke || '');
    node.setAttribute('stroke-width', this.#stage.options.strokeWidth?.toString() || '');
    node.setAttribute('fill-opacity', this.#stage.options.fillOpacity?.toString() || '');
    return node;
  };
  #EN2CN = () => {
    if (this.#stage.currentType === this.#stage.tools().RECT) {
      return '矩形';
    } else if (this.#stage.currentType === this.#stage.tools().ELLIPSE) {
      return '椭圆';
    } else if (this.#stage.currentType === this.#stage.tools().LINE) {
      return '直线';
    } else {
      return '路径';
    }
  };
  plugin = (d: string) => {
    this.#d = d;
  };
  isDrawing = () => {
    return this.#isDrawing;
  };
  disabled = (isDisabled: boolean) => {
    this.#isDisabled = isDisabled;
  };
}

export default Shape;
