import "./index.css";
import React from "react";
import { Stage } from "../../../lux";

interface State {
  history: Array<any>;
  index: number;
}

interface Props {}

class History extends React.Component {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      history: [],
      index: 0,
    };
  }

  init = () => {
    const stage = (window as any).stage as Stage;
    stage.history?.watch(() => {
      this.setState({
        history: stage.history?.get(),
        index: stage.history?.index(),
      });
    });
  };

  getIcon = (action: Symbol) => {
    const stage = (window as any).stage as Stage;
    if (action === stage.history?.actions().ADD) {
      return "#objects-panel";
    } else if (
      action === stage.history?.actions().SCALE ||
      action === stage.history?.actions().MOUSE_SCALE ||
      action === stage.history?.actions().MOUSE_MOVE
    ) {
      return "#transform-tool";
    } else if (
      action === stage.history?.actions().KEYBOARD_MOVE ||
      action === stage.history?.actions().DELETE ||
      action === stage.history?.actions().EDIT ||
      action === stage.history?.actions().NODE_X ||
      action === stage.history?.actions().NODE_Y ||
      action === stage.history?.actions().NODE_WIDTH ||
      action === stage.history?.actions().NODE_HEIGHT
    ) {
      return "#menu";
    }
  };

  handleHistory = (index: number) => {
    const stage = (window as any).stage as Stage;
    const index_compaire = this.state.index - index - 1;

    if (index_compaire > 0) {
      for (let i = 0; i < index_compaire; i++) {
        stage?.history?.undo();
      }
    } else if (index_compaire < 0) {
      for (let i = 0; i < Math.abs(index_compaire); i++) {
        stage?.history?.redo();
      }
    }
  };

  componentDidMount() {
    this.init();
  }

  componentWillUnmount() {}

  render = () => {
    return (
      <div id="history">
        <header>
          <h2>
            <x-message>History</x-message>
          </h2>
        </header>
        <section id="history-content" className="flex">
          <main id="history-main">
            <div id="items">
              {this.state.history.map((item, index) => (
                <div
                  style={{
                    background:
                      index === this.state.index - 1 ? "#3584e4" : "none",
                    color:
                      index === this.state.index - 1 ? "#ffffff" : "#333333",
                    opacity: index + 1> this.state.index  ? 0.5 : 1,
                  }}
                  className="item"
                  key={index}
                  skin="dock"
                  value={item.value}
                  onClick={() => this.handleHistory(index)}
                >
                  <x-icon
                    className="icon"
                    href={this.getIcon(item.action)}
                  ></x-icon>
                  <span className="label">
                    <x-message href="#add-object">{item.CN_label}</x-message>
                  </span>
                </div>
              ))}
            </div>
          </main>
        </section>
      </div>
    );
  };
}

export default History;
