import { SVGPathData } from 'svg-pathdata';
import Draggable from '../dependencies/draggable';
import { Stage } from '..';

class Select {
  #stage: Stage;
  #reprs?: SVGElement;
  #smartDistance = 10;
  #checkeds = new Map();
  #checkedsTexts = new Map();
  #checkedsPaths = new Map();
  #checkedsGroups = new Map();
  #uncheckeds = [] as Array<ChildNode>;
  #frame?: SVGElement;
  #cacheRect?: DOMRect;
  #startX = 0;
  #startY = 0;
  #cacheX = 0;
  #cahceY = 0;
  #focusNode?: SVGElement;
  #once: Boolean = false;
  #isMousedown: Boolean = false;
  #isDragging: Boolean = false;
  #isMoving: Boolean = false;
  #isDrawingRubber: Boolean = false;
  #canDrawRubber: Boolean = false;
  #isDisabled: Boolean = false;
  #disableDeletion: Boolean = false;
  #isDisDrag: Boolean = false;
  #smartGuides: Boolean = true;
  #callback?: Function;
  #frameCallback?: Function;
  #timer?: NodeJS.Timeout;
  #timeout = 10;
  #listen?: MutationObserver;
  #WATCH_ACTIONS = {
    SELECTED: Symbol(),
    FRAME: Symbol(),
  };
  constructor(stage: Stage) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
    this.#install();
    this.#listenerFrame();
  }
  #install = () => {
    this.#reprs = document.querySelector('#smart-guides-hud g') as SVGElement;
    this.#frame = document.querySelector('[uid="frame-select"]') as SVGElement;
    this.#stage.stage?.addEventListener('dblclick', this.#dblclick);
    this.#stage.stage?.addEventListener('mousedown', this.#mousedown);
    document.addEventListener('mousemove', this.#mousemove);
    document.addEventListener('mouseup', this.#mouseup);
  };
  watch = (action: Symbol, func: Function) => {
    if (action === this.actions().SELECTED) {
      this.#callback = func;
    } else if (action === this.actions().FRAME) {
      this.#frameCallback = func;
    }
  };

  /**
   * @name 监听选区变化
   */
  #listenerFrame = () => {
    this.#listen = new MutationObserver(() => {
      this.#frameCallback?.();
    });
    this.#listen.observe(this.frame() as Node, {
      attributes: true,
      attributeFilter: ['x', 'y', 'transform'],
    });
  };
  #dblclick = (ev: MouseEvent) => {
    if (ev.button !== 0) return;
    const currentTarget = ev.target as SVGElement;
    if (this.#stage.currentType === this.#stage.tools().SELECT) {
      if (this.size() === 1 && this.first().node.localName === 'g') {
        this.#stage.switchLayer(this.first().node);
        this.reset();
        this.#autoHighLight(ev);
      } else if (this.size() === 1 && this.first().node.localName === 'text') {
        this.#stage.tool(this.#stage.tools().TEXT, true);
      } else if (this.size() === 0) {
        this.#stage.switchLayer();
        this.reset();
      }
    } else if (this.#stage.currentType === this.#stage.tools().ASELECT) {
      if (currentTarget.localName === 'text') {
        this.#stage.tool(this.#stage.tools().TEXT, true);
      }
    }
  };
  #mousedown = (ev: MouseEvent) => {
    if (
      (this.#stage.currentType !== this.#stage.tools().SELECT &&
        this.#stage.currentType !== this.#stage.tools().ASELECT) ||
      this.#isDisabled ||
      this.#stage.resize?.isResizing()
    )
      return;
    this.#isMousedown = true;
    this.#canDrawRubber = false;
    this.#focusNode = this.areaFocus(ev) as SVGElement;
    if (this.#focusNode && ev.button === 0) this.#once = true;
    if (!this.#focusNode && ev.button !== 2) {
      this.#canDrawRubber = true;
    }
    this.#startX = ev.clientX;
    this.#startY = ev.clientY;
  };
  #mousemove = (ev: MouseEvent) => {
    if (!this.#isMousedown) return;
    if (this.#canDrawRubber) this.#drawRubber(ev);
    const { clientX, clientY, shiftKey } = ev;
    const d = Math.abs(this.#startX - clientX) + Math.abs(this.#startY - clientY);
    if (d >= 4 && this.#once && this.#focusNode) {
      this.#once = false;
      if (!shiftKey && !this.has(this.#focusNode)) {
        this.clear();
      }
      this.set(this.#focusNode, {
        node: this.#focusNode,
      });
      if (!this.#isDisDrag) this.drag(ev, this.#focusNode as Node);
    }
  };
  #mouseup = (ev: MouseEvent) => {
    if (!this.#isMousedown) return;
    this.#isMousedown = false;
    this.#drawRubberEnd(ev);
    if (!this.#focusNode || this.has(this.#focusNode)) return;
    if (!ev.shiftKey && !this.has(this.#focusNode)) {
      this.clear();
    }
    this.set(this.#focusNode, {
      node: this.#focusNode,
    });
    this.resetFrame(true);
  };
  drag = (ev: MouseEvent, node: Node) => {
    this.#uncheckeds = [this.#stage.boardView as SVGRectElement];
    const element = node as Element;
    const draggable = new Draggable();

    const { a: scale } = (this.#frame?.parentNode as any)?.getCTM();
    const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;

    draggable.install({
      element,
      start: () => {
        this.#isDragging = true;
        const xArr = [] as Array<number>;
        const yArr = [] as Array<number>;
        const cCTM = (this.#stage.canvas as SVGAElement)?.getCTM() as SVGMatrix;

        let p: any;
        this.get().forEach((item: any) => {
          const { node } = item;
          const parentNode = node.parentNode as SVGAElement;
          if (!p) p = parentNode;
          item.CTM = parentNode.getCTM() as SVGMatrix;
          item.matrix = node.getMatrix() as SVGMatrix;
          item.box = node.getBoundingClientRect() as DOMRect;
          const box = item.box;
          const minX = (box.x - left - cCTM.e) / cCTM.a;
          const maxX = (box.right - left - cCTM.e) / cCTM.a;
          const minY = (box.y - top - cCTM.f) / cCTM.d;
          const maxY = (box.bottom - top - cCTM.f) / cCTM.d;

          xArr.push(minX, maxX);
          yArr.push(minY, maxY);
        });

        p?.childNodes.forEach((ChildNode: ChildNode) => {
          if (ChildNode.nodeType === 3) return;
          if (!this.has(ChildNode)) this.#uncheckeds.push(ChildNode);
        });

        const maxX = Math.max.apply(null, xArr);
        const minX = Math.min.apply(null, xArr);
        const maxY = Math.max.apply(null, yArr);
        const minY = Math.min.apply(null, yArr);

        const width = maxX - minX;
        const height = maxY - minY;

        this.setFrame(minX, minY, width, height);
        if (!this.has(node)) this.toggleFrame(true);
        this.#setGrippiePosition(minX, minY, maxX, maxY, width, height);

        this.#cacheX = Number(this.#frame?.getAttribute('x'));
        this.#cahceY = Number(this.#frame?.getAttribute('y'));
        this.#cacheRect = this.#frame?.getBoundingClientRect() as DOMRect;
      },
      move: (ev: Draggable) => {
        if (!this.#isDragging) return;
        if (this.#reprs) this.#reprs.innerHTML = '';
        const { dataX, dataY } = ev;
        if (!this.#isMoving) {
          this.#stage.history?.add(this.#stage.history.actions().MOUSE_MOVE, '鼠标移动');
          this.toggleFrame(false);
          this.#isMoving = true;
        }

        this.#stage.hover?.closeOutline();
        const { sDx, sDy } = this.#comparePosition(dataX, dataY);
        this.get().forEach((item: any) => {
          const { node, CTM, matrix } = item;

          const ad = CTM.a * CTM.d;
          const bc = CTM.b * CTM.c;
          const dx = (CTM.d / (ad - bc)) * (dataX + sDx);
          const dy = (CTM.a / (ad - bc)) * (dataY + sDy);
          const ne = dx - (dy / CTM.a) * CTM.c;
          const nf = dy - (dx / CTM.d) * CTM.b;
          const e = (matrix.e + ne).toFixed(9);
          const f = (matrix.f + nf).toFixed(9);
          const { a, b, c, d } = matrix;

          // const cm = node?.ownerSVGElement.createSVGMatrix();

          // cm.a = a;
          // cm.b = b;
          // cm.c = c;
          // cm.d = d;
          // cm.e = e;
          // cm.f = f;

          // const t = node?.ownerSVGElement.createSVGTransformFromMatrix(cm);
          // node.transform.baseVal.initialize(t);
          node.setAttribute('transform', `matrix(${a},${b},${c},${d},${e},${f})`);
        });

        const fx = (this.#cacheX + (sDx + dataX) / scale).toFixed(9);
        const fy = (this.#cahceY + (sDy + dataY) / scale).toFixed(9);
        this.#frame?.setAttribute('x', fx);
        this.#frame?.setAttribute('y', fy);
        this.resetFrame(false);

        // const box = item.box;
        //   const minX = (box.x - left - cCTM.e) / cCTM.a;
        //   const maxX = (box.right - left - cCTM.e) / cCTM.a;
        //   const minY = (box.y - top - cCTM.f) / cCTM.a;
        //   const maxY = (box.bottom - top - cCTM.f) / cCTM.a;
        //   xArr.push(minX, maxX);
        //   yArr.push(minY, maxY);

        // const maxX = Math.max.apply(null, xArr);
        // const minX = Math.min.apply(null, xArr);
        // const maxY = Math.max.apply(null, yArr);
        // const minY = Math.min.apply(null, yArr);

        // const width = maxX - minX;
        // const height = maxY - minY;

        // this.setFrame(minX + dx / scale, minY + dy / scale, width, height);
        // this.#setGrippiePosition(
        //   minX + dx / scale,
        //   minY + dy / scale,
        //   maxX + dx / scale,
        //   maxY + dy / scale,
        //   width,
        //   height
        // );
      },
      end: () => {
        if (!this.#isDragging) return;
        if (this.#reprs) this.#reprs.innerHTML = '';

        setTimeout(() => {
          this.#isDragging = false;
          this.#isMoving = false;
        });

        draggable?.uninstall();
        this.resetFrame(true);
      },
    });
    draggable?.mousedown(ev);
  };
  #drawRubber = (ev: MouseEvent) => {
    if (this.#stage.resize?.isResizing() || this.#isDragging) return;
    const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
    const m = this.#stage.matrix;
    const xArr = [(this.#startX - left - m.e) / m.a, (ev.clientX - left - m.e) / m.a];
    const yArr = [(this.#startY - top - m.f) / m.a, (ev.clientY - top - m.f) / m.a];

    const maxX = Math.max.apply(null, xArr);
    const minX = Math.min.apply(null, xArr);
    const maxY = Math.max.apply(null, yArr);
    const minY = Math.min.apply(null, yArr);

    const width = maxX - minX;
    const height = maxY - minY;

    this.#setRubberhand(minX, minY, width, height);
    this.#isDrawingRubber = true;
  };
  #drawRubberEnd = (ev: MouseEvent) => {
    if (!this.#isDrawingRubber) return;
    setTimeout(() => {
      this.#isDrawingRubber = false;
    });

    if (this.#startX === ev.clientX && this.#startY === ev.clientY) return;
    if (this.#stage.resize?.isResizing()) return;
    if (!ev.shiftKey && this.size() > 0) this.clear();

    const r = document.querySelector('[uid="rubber-hand"]') as SVGElement;
    const rBox = r?.getBoundingClientRect() as DOMRect;

    const xArr = [] as Array<number>;
    const yArr = [] as Array<number>;
    if (this.#stage.currentType === this.#stage.tools().SELECT) {
      const len = this.#stage.layer.children.length;
      for (let i = 0; i < len; i++) {
        const node = this.#stage.layer.children[i];
        const box = node.getBoundingClientRect() as DOMRect;
        if (
          (window.getComputedStyle(node).pointerEvents !== 'none' &&
            this.#stage.CAN_CEHCKED_NODE.includes(node.localName) &&
            this.checkIntersect(rBox, box)) ||
          (ev.shiftKey && this.has(node))
        ) {
          xArr.push(box.x, box.right);
          yArr.push(box.y, box.bottom);
          this.set(node, { node });
        }
      }
    } else {
      const iterator = document.createNodeIterator(
        this.#stage.svgRoot as SVGElement,
        NodeFilter.SHOW_ELEMENT,
      );
      let node = iterator.nextNode() as any;
      while (node !== null) {
        const box = node.getBoundingClientRect() as DOMRect;
        if (
          (window.getComputedStyle(node).pointerEvents !== 'none' &&
            this.#stage.CAN_CEHCKED_NODE.includes(node.localName) &&
            this.checkIntersect(rBox, box) &&
            node.localName !== 'g') ||
          (ev.shiftKey && this.has(node))
        ) {
          xArr.push(box.x, box.right);
          yArr.push(box.y, box.bottom);
          this.set(node, { node });
        }
        node = iterator.nextNode();
      }
    }

    const { a, e, f } = this.#stage.matrix;
    const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
    const minX = (Math.min.apply(null, xArr) - left - e) / a;
    const maxX = (Math.max.apply(null, xArr) - left - e) / a;
    const minY = (Math.min.apply(null, yArr) - top - f) / a;
    const maxY = (Math.max.apply(null, yArr) - top - f) / a;

    //选区大小设置
    this.setFrame(
      Math.abs(minX) === Infinity ? 0 : minX,
      Math.abs(minY) === Infinity ? 0 : minY,
      Math.abs(maxX - minX) === Infinity ? 0 : maxX - minX,
      Math.abs(maxY - minY) === Infinity ? 0 : maxY - minY,
    );
    this.#setRubberhand(0, 0, 0, 0);
    this.resetFrame(false);
  };
  #setRubberhand(x: number, y: number, width: number, height: number) {
    const r = document.querySelector('[uid="rubber-hand"]') as SVGRectElement;
    r?.setAttribute('x', x.toFixed(9));
    r?.setAttribute('y', y.toFixed(9));
    r?.setAttribute('width', width.toFixed(9));
    r?.setAttribute('height', height.toFixed(9));
  }
  nodesRemove = () => {
    if (this?.size() === 0 || this.#disableDeletion) return;
    this.#stage.history?.add(this.#stage.history.actions().DELETE, '删除');
    this.get().forEach((item: any) => item.node.remove());
    this.clear();
    this.reset();
    this.#stage.history?.end();
  };
  reset = () => {
    this.clear();
    this.setFrame(0, 0, 0, 0);
    this.#stage.text?.quit();
  };
  checkedsTexts = () => {
    return this.#checkedsTexts;
  };
  checkedsPaths = () => {
    return this.#checkedsPaths;
  };
  checkedsGroups = () => {
    return this.#checkedsGroups;
  };
  set = (key: any, value: any) => {
    switch (key.localName) {
      case 'text':
        this.#checkedsTexts.set(key, key);
        break;
      case 'path':
        this.#checkedsPaths.set(key, key);
        break;
      case 'g':
        this.#checkedsGroups.set(key, key);
        const tArr = key.querySelectorAll('text');
        tArr.forEach((node: Node) => {
          this.#checkedsTexts.set(node, node);
        });
        const pArr = key.querySelectorAll('path');
        pArr.forEach((node: Node) => {
          this.#checkedsPaths.set(node, node);
        });
        break;
    }
    this.#checkeds.set(key, value);
    clearTimeout(this.#timer);
    this.#timer = setTimeout(() => {
      this.#callback?.();
    }, this.#timeout);
  };
  has = (node: any) => {
    return this.#checkeds.has(node);
  };
  get = () => {
    return this.#checkeds;
  };
  item = (key?: any) => {
    return this.#checkeds.get(key);
  };
  first = () => {
    return this.item(this.#checkeds.keys().next().value);
  };
  size = () => {
    return this.#checkeds.size;
  };
  delete = (key: any) => {
    switch (key.localName) {
      case 'text':
        this.#checkedsTexts.delete(key);
        break;
      case 'path':
        this.#checkedsPaths.delete(key);
        break;
      case 'g':
        this.#checkedsGroups.delete(key);
        const tArr = key.querySelectorAll('text');
        tArr.forEach((node: Node) => {
          this.#checkedsTexts.delete(node);
        });
        const pArr = key.querySelectorAll('path');
        pArr.forEach((node: Node) => {
          this.#checkedsPaths.delete(node);
        });
        break;
    }
    this.#checkeds.delete(key);
    clearTimeout(this.#timer);
    this.#timer = setTimeout(() => {
      this.#callback?.();
    }, this.#timeout);
  };
  clear = () => {
    this.#checkeds.clear();
    this.#checkedsTexts.clear();
    this.#checkedsPaths.clear();
    this.#checkedsGroups.clear();
    clearTimeout(this.#timer);
    this.#timer = setTimeout(() => {
      this.#callback?.();
    }, this.#timeout);
  };
  frame = () => {
    return this.#frame;
  };
  areaFocus = (ev: MouseEvent) => {
    let node: Node | any;
    const p = this.#expandArea(ev.clientX, ev.clientY);
    if (this.#stage.currentType === this.#stage.tools().SELECT) {
      const len = this.#stage.layer.children.length;
      outerLoop: for (let i = 0; i < p.length; i++) {
        const n = document.elementFromPoint(p[i].x, p[i].y);
        for (let j = 0; j < len; j++) {
          if (this.#stage.layer.children[j].contains(n)) {
            node = this.#stage.layer.children[j];
            break outerLoop;
          }
        }
      }
    } else {
      for (let i = 0; i < p.length; i++) {
        const item = p[i];
        const n = document.elementFromPoint(item.x, item.y);
        if (this.#stage.svgRoot?.contains(n) && n?.localName !== 'g') {
          node = n;
          break;
        }
      }
    }

    if (!node) {
      if (!ev.shiftKey) this.reset();
      return;
    }
    //元素如果被锁定不做处理
    if (window.getComputedStyle(node).pointerEvents === 'none') return;
    // if (node.localName === "tspan") node = this.queryTspanText(node);
    return node;
  };
  #expandArea = (x: number, y: number) => {
    const arr = [] as Array<any>;
    arr.push({ x, y }); //原点
    const expend = 3;
    for (let i = 1; i <= expend; i++) {
      arr.push({ x: x - i, y: y - i }); //左上
      arr.push({ x, y: y - 1 }); //上
      arr.push({ x: x + i, y: y + i }); //右上
      arr.push({ x: x - i, y }); //左
      arr.push({ x: x + i, y }); //右
      arr.push({ x: x - i, y: y + i }); //左下
      arr.push({ x, y: y + i }); //下
      arr.push({ x: x + i, y: y + i }); //右下
    }
    return arr;
  };
  resetFrame = (resetHand?: boolean) => {
    this.#stage.hover?.closeOutline();
    if (this.size() < 1) return this.reset();
    const xArr = [] as Array<number>;
    const yArr = [] as Array<number>;
    const m = this.#stage.matrix;

    const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
    if (resetHand) {
      this.get().forEach((item: any) => {
        const node = item.node;
        if (node.localName !== 'text') {
          node.style.vectorEffect = 'none';
          node.style.strokeWidth = '';
        }
        const box = node.getBoundingClientRect() as DOMRect;
        const minX = (box.x - left - m.e) / m.a;
        const maxX = (box.right - left - m.e) / m.a;
        const minY = (box.y - top - m.f) / m.a;
        const maxY = (box.bottom - top - m.f) / m.a;
        xArr.push(minX, maxX);
        yArr.push(minY, maxY);
        this.#nodeMatrix(item.node);
      });
    } else {
      const box = this.#frame?.getBoundingClientRect() as DOMRect;
      const minX = (box?.x - left - m.e) / m.a;
      const maxX = (box?.right - left - m.e) / m.a;
      const minY = (box?.y - top - m.f) / m.a;
      const maxY = (box?.bottom - top - m.f) / m.a;
      xArr.push(minX, maxX);
      yArr.push(minY, maxY);
    }

    const maxX = Math.max.apply(null, xArr);
    const minX = Math.min.apply(null, xArr);
    const maxY = Math.max.apply(null, yArr);
    const minY = Math.min.apply(null, yArr);
    //选区
    const width = maxX - minX;
    const height = maxY - minY;
    if (resetHand) {
      //选区大小设置
      this.setFrame(minX, minY, width, height);
    }
    this.#setGrippiePosition(minX, minY, maxX, maxY, width, height);
  };
  /**
   * @name 设置框选宽高位置
   * @param x
   * @param y
   * @param width
   * @param height
   */
  setFrame = (x: number, y: number, width: number, height: number) => {
    const f = this.#frame as any;
    f.setAttribute('x', x.toFixed(9));
    f.setAttribute('y', y.toFixed(9));
    f.setAttribute('width', Math.abs(width).toFixed(9));
    f.setAttribute('height', Math.abs(height).toFixed(9));

    if (
      width > 0 &&
      height > 0 &&
      (this.#stage.currentType === this.#stage.tools().SELECT ||
        this.#stage.currentType === this.#stage.tools().ASELECT)
    ) {
      //显示
      this.toggleFrame(false);
    } else {
      //隐藏
      this.toggleFrame(true);
    }
  };
  toggleFrame = (hidden = false) => {
    const oG = document.querySelector('[uid="origin-grippie"]') as SVGElement;
    const sG = document.querySelector('[uid="scale-grippies"]') as SVGElement;
    if (!hidden) {
      this.#frame?.removeAttribute('opacity');
      oG.style.visibility = 'visible';
      sG.style.visibility = 'visible';
    } else {
      this.#frame?.setAttribute('opacity', '0');
      oG.style.visibility = 'hidden';
      sG.style.visibility = 'hidden';
    }
  };
  /**
   * @name 设置锚点位置
   * @param minX
   * @param minY
   * @param maxX
   * @param maxY
   * @param width
   * @param height
   */
  #setGrippiePosition = (
    minX: number,
    minY: number,
    maxX: number,
    maxY: number,
    width: number,
    height: number,
  ) => {
    const tHud = document.querySelector('#transform-hud');
    const grippie = tHud?.querySelectorAll('.grippie') as NodeListOf<Element>;
    const cSize = Number(grippie[0].getAttribute('width'));

    const x0 = (minX - cSize / 2 + width / 2).toFixed(9);
    const y0 = (minY - cSize / 2).toFixed(9);
    grippie[0].setAttribute('x', x0);
    grippie[0].setAttribute('y', y0);

    const x1 = (minX - cSize / 2).toFixed(9);
    const y1 = (minY - cSize / 2 + height / 2).toFixed(9);
    grippie[1].setAttribute('x', x1);
    grippie[1].setAttribute('y', y1);

    const x2 = (minX - cSize / 2 + width / 2).toFixed(9);
    const y2 = (minY - cSize / 2 + height).toFixed(9);
    grippie[2].setAttribute('x', x2);
    grippie[2].setAttribute('y', y2);

    const x3 = (minX - cSize / 2 + width).toFixed(9);
    const y3 = (minY - cSize / 2 + height / 2).toFixed(9);
    grippie[3].setAttribute('x', x3);
    grippie[3].setAttribute('y', y3);

    const x4 = (minX - cSize / 2).toFixed(9);
    const y4 = (minY - cSize / 2).toFixed(9);
    grippie[4].setAttribute('x', x4);
    grippie[4].setAttribute('y', y4);

    const x5 = (maxX - cSize / 2).toFixed(9);
    const y5 = (minY - cSize / 2).toFixed(9);
    grippie[5].setAttribute('x', x5);
    grippie[5].setAttribute('y', y5);

    const x6 = (minX - cSize / 2).toFixed(9);
    const y6 = (maxY - cSize / 2).toFixed(9);
    grippie[6].setAttribute('x', x6);
    grippie[6].setAttribute('y', y6);

    const x7 = (maxX - cSize / 2).toFixed(9);
    const y7 = (maxY - cSize / 2).toFixed(9);
    grippie[7].setAttribute('x', x7);
    grippie[7].setAttribute('y', y7);

    const gSize = (this.#stage.options.grippieSize || 11) / this.#stage.matrix.a;
    const x = minX - gSize / 2 + width / 2;
    const y = minY - gSize / 2 + height / 2;
    const oG = document.querySelector('[uid="origin-grippie"]');

    oG?.setAttribute('x', x.toFixed(9));
    oG?.setAttribute('y', y.toFixed(9));
  };
  /**
   * @name 自动高亮
   * @param ev
   */
  #autoHighLight = (ev: any) => {
    const paths = ev.path || ev.composedPath();
    outerLoop: for (let i = 0; i < paths.length; i++) {
      for (let j = 0; j < this.#stage.layer.children.length; j++) {
        if (paths[i] instanceof Node && this.#stage.layer?.children[j]?.contains(paths[i])) {
          this.#stage.hover?.highLight(this.#stage.layer?.children[j]);
          break outerLoop;
        }
      }
    }
  };
  #comparePosition = (dx: number, dy: number) => {
    let sDx = 0;
    let sDy = 0;
    if (!this.#smartGuides) {
      return {
        sDx,
        sDy,
      };
    }
    const { a, e, f } = this.#stage.matrix;
    const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;

    let hl: any = void 0;
    let vl: any = void 0;

    this.#uncheckeds.forEach((ChildNode: ChildNode) => {
      if (ChildNode.nodeType === 3) return;

      const el = ChildNode as SVGElement;
      const { x, y, bottom, right } = el.getBoundingClientRect() as DOMRect;

      const nl = Number(this.#cacheRect?.x) + dx;
      const nr = Number(this.#cacheRect?.right) + dx;

      if (
        Math.abs(nl - x) < this.#smartDistance ||
        Math.abs(nl - right) < this.#smartDistance ||
        Math.abs(nr - right) < this.#smartDistance ||
        Math.abs(nr - x) < this.#smartDistance
      ) {
        const minY = Math.min(Number(this.#cacheRect?.top) + dy, y);
        const maxY = Math.max(Number(this.#cacheRect?.bottom) + dy, bottom);
        const y1 = (minY - top - f) / a;
        const y2 = (maxY - top - f) / a;
        let x1 = 0;
        if (Math.abs(nl - x) < this.#smartDistance) {
          sDx = x - nl;
          x1 = (x - left - e) / a;
        } else if (Math.abs(nl - right) < this.#smartDistance) {
          sDx = right - nl;
          x1 = (right - left - e) / a;
        } else if (Math.abs(nr - right) < this.#smartDistance) {
          sDx = right - nr;
          x1 = (right - left - e) / a;
        } else {
          sDx = x - nr;
          x1 = (x - left - e) / a;
        }

        hl = {
          x1,
          x2: x1,
          y1,
          y2,
        };
      }

      const nt = Number(this.#cacheRect?.y) + dy;
      const nb = Number(this.#cacheRect?.bottom) + dy;

      if (
        Math.abs(nt - y) < this.#smartDistance ||
        Math.abs(nt - bottom) < this.#smartDistance ||
        Math.abs(nb - bottom) < this.#smartDistance ||
        Math.abs(nb - y) < this.#smartDistance
      ) {
        const minX = Math.min(Number(this.#cacheRect?.x) + dx, x);
        const maxX = Math.max(Number(this.#cacheRect?.right) + dx, right);
        const x1 = (minX - left - e) / a;
        const x2 = (maxX - left - e) / a;
        let y1 = 0;
        if (Math.abs(nt - y) < this.#smartDistance) {
          sDy = y - nt;
          y1 = (y - top - f) / a;
        } else if (Math.abs(nt - bottom) < this.#smartDistance) {
          sDy = bottom - nt;
          y1 = (bottom - top - f) / a;
        } else if (Math.abs(nb - bottom) < this.#smartDistance) {
          sDy = bottom - nb;
          y1 = (bottom - top - f) / a;
        } else {
          sDy = y - nb;
          y1 = (y - top - f) / a;
        }

        vl = {
          x1,
          x2,
          y1,
          y2: y1,
        };
      }
    });

    hl && this.#smartLine(hl.x1, hl.y1, hl.x2, hl.y2);
    vl && this.#smartLine(vl.x1, vl.y1, vl.x2, vl.y2);

    return {
      sDx,
      sDy,
    };
  };
  #smartLine = (x1: number, y1: number, x2: number, y2: number) => {
    const line = document.createElementNS('http://www.w3.org/2000/svg', 'line');
    line.setAttribute('x1', x1.toFixed(4));
    line.setAttribute('y1', y1.toFixed(4));
    line.setAttribute('x2', x2.toFixed(4));
    line.setAttribute('y2', y2.toFixed(4));
    line.classList.add('repr');
    this.#reprs?.append(line);
  };
  checkIntersect = (RectA: DOMRect, RectB: DOMRect): boolean => {
    const minx = Math.max(RectA.x, RectB.x);
    const miny = Math.max(RectA.y, RectB.y);
    const maxx = Math.min(RectA.right, RectB.right);
    const maxy = Math.min(RectA.bottom, RectB.bottom);
    if (minx > maxx || miny > maxy) return false;
    return true;
  };
  #nodeMatrix = (node: SVGElement) => {
    const parent = node.parentNode as SVGElement;
    if (!parent) return;
    const m = node.getMatrix();
    if (m.b !== 0 || m.c !== 0) {
      return this.#stage.history?.end();
    }

    const n = node as SVGAElement;
    const { x, y, width, height } = n.getBBox() as SVGRect;
    const nw = width * Math.abs(m.a);
    const nh = height * Math.abs(m.d);
    const nx = x * m.a + m.e + (m.a < 0 && -nw);
    const ny = y * m.d + m.f + (m.d < 0 && -nh);

    // try {
    //   const { a, b, c, d, e, f } = node?.transform.baseVal?.getItem(0)?.matrix;
    //   node?.transform.baseVal.clear();
    //   node.setAttribute("transform", `matrix(${a},${b},${c},${d},${e},${f})`);
    // } catch (error) {}
    const includesLocalName = ['g', 'image', 'text'];
    if (!includesLocalName.includes(node.localName)) {
      node.removeAttribute('transform');
    }

    switch (node.localName) {
      case 'g':
      case 'image':
      case 'text':
        break;
      // case "text":
      //   if (m.a === m.d && (m.e !== 0 || m.f !== 0)) {
      //     node.removeAttribute("transform");
      //     const y = parseFloat(node.getAttribute("y") || "");
      //     let fontSize = parseFloat(node.getAttribute("font-size") || "");
      //     node.setAttribute("font-size", (fontSize * m.a).toFixed(4));
      //     node.setAttribute("x", nx.toFixed(4));
      //     node.setAttribute("y", (y * m.a + m.f).toFixed(4));
      //   }
      //   break;
      case 'rect':
        node.setAttribute('x', nx.toFixed(4));
        node.setAttribute('y', ny.toFixed(4));
        node.setAttribute('width', nw.toFixed(4));
        node.setAttribute('height', nh.toFixed(4));
        break;
      case 'circle':
      case 'ellipse':
        node.setAttribute('cx', (nx + nw / 2).toFixed(4));
        node.setAttribute('cy', (ny + nh / 2).toFixed(4));
        node.setAttribute('rx', (nw / 2).toFixed(4));
        node.setAttribute('ry', (nh / 2).toFixed(4));
        break;
      case 'line':
        const x1 = Number(node.getAttribute('x1'));
        const y1 = Number(node.getAttribute('y1'));
        const x2 = Number(node.getAttribute('x2'));
        const y2 = Number(node.getAttribute('y2'));
        const nx1 = x1 * m.a + m.e;
        const ny1 = y1 * m.d + m.f;
        const nx2 = x2 * m.a + m.e;
        const ny2 = y2 * m.d + m.f;
        node.setAttribute('x1', nx1.toFixed(4));
        node.setAttribute('y1', ny1.toFixed(4));
        node.setAttribute('x2', nx2.toFixed(4));
        node.setAttribute('y2', ny2.toFixed(4));
        break;
      case 'path':
        const d = node.getAttribute('d') as string;
        const pathData = new SVGPathData(d).matrix(m.a, m.b, m.c, m.d, m.e, m.f).encode();
        node.setAttribute('d', pathData);
        break;
      case 'polyline':
      case 'polygon':
        let p = '';
        const polygon = node as SVGPolygonElement;
        for (let t = 0; t < polygon.points.numberOfItems; t += 1) {
          const s = polygon.points.getItem(t);
          const x = s.x * m.a + m.e;
          const y = s.y * m.d + m.f;
          p += `${x.toFixed(9)} ${y.toFixed(9)} `;
        }
        node.setAttribute('points', p);
        break;
    }

    this.#stage.history?.end();
  };
  actions = () => {
    return this.#WATCH_ACTIONS;
  };
  isDragging = () => {
    return this.#isDragging;
  };
  isDrawingRubber = () => {
    return this.#isDrawingRubber;
  };
  toggleSmartGuides = (open: boolean) => {
    this.#smartGuides = open;
    return this.#smartGuides;
  };
  disabled = (isDisabled: boolean) => {
    this.#isDisabled = isDisabled;
  };
  disableDeletion = (isDisabled: boolean) => {
    this.#disableDeletion = isDisabled;
  };
  disDrag = (isDisabled: boolean) => {
    this.#isDisDrag = isDisabled;
  };
  uninstall = () => {
    this.#callback = void 0;
    this.#frameCallback = void 0;
    this.#stage.stage?.removeEventListener('dblclick', this.#dblclick);
    this.#stage.stage?.removeEventListener('mousedown', this.#mousedown);
    document.removeEventListener('mousemove', this.#mousemove);
    document.removeEventListener('mouseup', this.#mouseup);
  };
}
export default Select;
