/**
 * @name stage-layout
 * @param matrix
 * @param grippieSize
 * @returns
 */
const layout = (matrix: any, grippieSize: number) => {
  return `<g uid="canvas" transform="matrix(${matrix.a},0,0,${matrix.d},${matrix.e},${matrix.f})">
    <style>
      #background-outlines rect{
        fill: white;
        stroke: rgb(0 0 0 / 20%);
        vector-effect: non-scaling-stroke;
      }

      #lux-svg-root .lux-disabled{
        opacity:0.24 !important;
      }
    </style>
    <g id="background-outlines">
        <rect style="pointer-events: none;" x="0" y="0" width="500" height="500" />
    </g>

    <g id="lux-workspaces">
        <g id="lux-svg-root"></g>
    </g>

    <g id="huds">
        <g id="outline-hud" context="transform">
            <style>
                #outline-hud [uid="outline"] {
                fill: none;
                stroke: rgb(245, 92, 54);
                stroke-width: 2;
                pointer-events: none;
                vector-effect: non-scaling-stroke;
                }
            </style>
            <path uid="outline"></path>
        </g>

        <g id="smart-guides-hud" enabled="">
            <style>
                #smart-guides-hud .repr {
                fill: none;
                stroke: blue;
                stroke-width: 1;
                vector-effect: non-scaling-stroke;
                opacity: 0.6;
                pointer-events: none;
                }
            </style>
            <g uid="reprs"></g>
        </g>

        <g id="spline-hud" mode="edit">
        <style>
            #spline-hud [uid="grippies"] {
            cursor: default;
            }
            #spline-hud [uid="grippies"]:focus {
            outline: none;
            }

            /**
             * Primary node grippies
             */

            #spline-hud .primary-node-grippie .outer-circle {
            fill: white;
            stroke: #4d2782;
            stroke-width: 2;
            vector-effect: non-scaling-stroke;
            }
            #spline-hud .primary-node-grippie[data-selected] .outer-circle {
            fill: #bc9aec;
            }
            #spline-hud #grippies:focus .primary-node-grippie[data-selected] .outer-circle {
            fill: #a370ea;
            }
            #spline-hud[mode^="draw-"]   .primary-node-grippie[data-position="mid"],
            #spline-hud[mode$="-basis"]  .primary-node-grippie:not([data-subpath-type="basis"]),
            #spline-hud[mode$="-rom"]    .primary-node-grippie:not([data-subpath-type="rom"]),
            #spline-hud[mode$="-levien"] .primary-node-grippie:not([data-subpath-type="levien"]) {
            display: none;
            }

            #spline-hud .primary-node-grippie .inner-circle {
            pointer-events: none;
            fill: #4d2782;
            }

            /**
             * Secondary node grippies
             */

            #spline-hud .secondary-node-grippie {
            fill: white;
            stroke: #ef6c6c;
            stroke-width: 1.5;
            vector-effect: non-scaling-stroke;
            }
            #spline-hud[mode^="draw-"]   .secondary-node-grippie,
            #spline-hud[mode$="-basis"]  .secondary-node-grippie:not([data-subpath-type="basis"]),
            #spline-hud[mode$="-rom"]    .secondary-node-grippie:not([data-subpath-type="rom"]),
            #spline-hud[mode$="-levien"] .secondary-node-grippie:not([data-subpath-type="levien"]) {
            display: none;
            }

            /**
             * Helper lines
             */

            #spline-hud .helper-line {
            fill: none;
            stroke: red;
            stroke-width: 2;
            vector-effect: non-scaling-stroke;
            pointer-events: none;
            opacity: 0.3;
            }
            #spline-hud[mode^="draw-freehand"] .helper-line,
            #spline-hud[mode$="-rom"] .helper-line {
            display: none;;
            }

            /**
             * Curve grippies
             */

            #spline-hud[mode^="draw"] .curve-grippie {
            display: none;
            }
            #spline-hud .curve-grippie path {
            fill: none;
            stroke: blue;
            stroke-width: 10px;
            vector-effect: non-scaling-stroke;
            opacity: 0;
            }
            #spline-hud .curve-grippie path:hover {
            opacity: 0.2;
            }
        </style>

        <g uid="grippies" tabindex="-1">
            <g uid="helper-lines"></g>
            <g uid="curve-grippies" style=""></g>
            <g uid="primary-node-grippies"></g>
            <g uid="secondary-node-grippies"></g>
        </g>
        </g>

        <g id="cubic-bezier-seg-hud" drawing>
        <style>
            #cubic-bezier-seg-hud [uid="control-line-1"],
            #cubic-bezier-seg-hud [uid="control-line-2"] {
            fill: none;
            stroke: red;
            stroke-width: 1;
            vector-effect: non-scaling-stroke;
            pointer-events: none;
            opacity: 0.4;
            }

            #cubic-bezier-seg-hud [uid="control-point-1-grippie"],
            #cubic-bezier-seg-hud [uid="control-point-2-grippie"] {
            fill: red;
            stroke: none;
            pointer-events: none;
            opacity: 1;
            }

            #cubic-bezier-seg-hud [uid="outline"] {
            fill: none;
            stroke: red;
            stroke-width: 2.5;
            vector-effect: non-scaling-stroke;
            pointer-events: none;
            }
        </style>

        <g uid="container" transform="matrix(1, 0, 0, 1, 0, 0)" style="visibility: hidden;">
            <path uid="outline"></path>
            <line uid="control-line-1" x2="521.933" y1="141.132" x1="521.933" y2="141.132"></line>
            <line uid="control-line-2" x2="745.073" y1="149.396" x1="745.073" y2="149.396"></line>
            <circle uid="control-point-1-grippie" r="2.86078" cx="-9999999" cy="-9999999"></circle>
            <circle uid="control-point-2-grippie" r="2.86078" cx="-9999999" cy="-9999999"></circle>
        </g>
        </g>

        <g id="text-hud" tabindex="-1">
        <style>
        
            /**
            * Overlays
            */
            #text-hud{
                pointer-events: none;
            }

            #text-hud [uid="caret-line"] {
                stroke-width: 3;
                vector-effect: non-scaling-stroke;
                pointer-events: none;
                stroke: gray;
            }

            #text-hud[inputmode] [uid="caret-line"] {
                stroke: blue;
            }
            
            #text-hud [uid="selection-rects"] rect {
                fill:rgba(179, 179, 179, 0.3);
                pointer-events: none;
                stroke:none;
            }

            #text-hud[inputmode] [uid="selection-rects"] rect {
                fill: rgb(53 132 228 / 20%);
            }
            
            #text-hud [uid="html-input-object"] {
                width: 1px;
                height: 1px;
                opacity: 0;
                background: white;
                pointer-events: none;
            }


            #text-hud [uid="html-input-object"] {
            /* Needed for correct positioning of IME dialog */
            font-size: 10px !important;
            }
            

            #text-hud [uid="html-input"] {
                width: 100%;
                height: 100%;
                padding: 0;
                margin: 0;
                white-space: pre;
                text-align:left;
                -webkit-user-modify: read-write-plaintext-only;
                -webkit-user-select: text;
            }

        </style>

        <g uid="overlays">
            <line uid="caret-line"></line>
            <g uid="selection-rects"></g>
        </g>

        <foreignObject uid="html-input-object" width="200" height="200" x="0" y="0">
            <p uid="html-input" tabindex="0"></p>
        </foreignObject>
        </g>

        <g id="frame-select-hud">
            <style>
                #frame-select-hud [uid="frame-select"] {
                    // stroke-dasharray: 4;
                    fill-opacity: 0;
                    stroke: #1F83EA;
                    stroke-width: 1;
                    pointer-events: none;
                    vector-effect: non-scaling-stroke;
                    visibility:hidden;
                }
            </style>
            <rect uid="frame-select" x="-99999" y="-99999" width="0" height="0" ></rect>
        </g>

        <g id="freehand-hud" drawing="">
            <style>
                #freehand-hud [uid="outline"] {
                fill: none;
                stroke: red;
                stroke-width: 2.5;
                vector-effect: non-scaling-stroke;
                pointer-events: none;
                }
            </style>

            <polyline uid="outline"></polyline>
        </g>

        <g id="view-hud" style="visibility:hidden">
            <style>
                #view-hud .grippie[data-position="left"],
                #view-hud .grippie[data-position="right"],
                #view-hud .grippie[data-position="top"],
                #view-hud .grippie[data-position="bottom"] {
                stroke: red;
                vector-effect: non-scaling-stroke;
                stroke-width: 8;
                opacity: 0;
                }

                #view-hud .grippie[data-position="top-left"] path,
                #view-hud .grippie[data-position="top-right"] path,
                #view-hud .grippie[data-position="bottom-left"] path,
                #view-hud .grippie[data-position="bottom-right"] path {

                stroke-width: 1;
                vector-effect: non-scaling-stroke;
                fill: rgb(176, 214, 255);
                stroke: rgb(2, 81, 165);
                }

                #view-hud .grippie[data-position="left"],
                #view-hud .grippie[data-position="right"] {
                cursor: ew-resize;
                }
                #view-hud .grippie[data-position="top"],
                #view-hud .grippie[data-position="bottom"] {
                cursor: ns-resize;
                }
                #view-hud .grippie[data-position="top-left"] {
                cursor: nwse-resize;
                }
                #view-hud .grippie[data-position="top-right"] {
                cursor: nesw-resize;
                }
                #view-hud .grippie[data-position="bottom-left"] {
                cursor: nesw-resize;
                }
                #view-hud .grippie[data-position="bottom-right"] {
                cursor: nwse-resize;
                }
       
            </style>

            <g uid="subhuds">
                <g class="grippie" data-position="top-left" transform="matrix(1, 0, 0, 1, -15, -15)">
                    <path d="M 15 15 h 15 v -5 h -20 v 20 h 5 z"></path>
                </g>

                <g class="grippie" data-position="top-right" transform="matrix(1, 0, 0, 1, 548, -15)">
                    <path d="M 0 15 h 15 v 15 h 5 v -20 h -20 z"></path>
                </g>

                <g class="grippie" data-position="bottom-left" transform="matrix(1, 0, 0, 1, -15, 548)">
                    <path d="M 15 15 h 15 v 5 h -20 v -20 h 5 z"></path>
                </g>

                <g class="grippie" data-position="bottom-right" transform="matrix(1, 0, 0, 1, 548, 548)">
                    <path d="M 15 15 v -15 h 5 v 20 h -20 v -5 z"></path>
                </g>
                </g>
            </g>
        
        <g id="rubber-band-hud">
            <style>
                #rubber-band-hud [uid="rubber-hand"] {
                    fill: rgb(12 118 218 / 40%);
                    stroke: rgb(12 118 218 / 100%);
                    stroke-width: 1;
                    pointer-events: none;
                    vector-effect: non-scaling-stroke;
                }
            </style>
            <rect uid="rubber-hand" x="0" y="0" width="0" height="0"></rect>
        </g>

        <g id="transform-hud">
        <style>
            #transform-hud .grippie {
            fill: #ffffff;
            stroke: #C3CBDD;
            stroke-width: 1;
            vector-effect: non-scaling-stroke;
            fill: rgb(46, 102, 249);
            stroke: white;
            stroke-width: 2;
            filter: url(#grippie-shadow);
            filter: drop-shadow(0px 0px 1px black);
            }
        </style>
        <g uid="grippies">
            <g uid="scale-grippies" style="visibility:hidden;"><rect class="grippie"  data-position="top" width="${grippieSize}" height="${grippieSize}">
            </rect><rect class="grippie" data-position="left" width="${grippieSize}" height="${grippieSize}">
            </rect><rect class="grippie" data-position="bottom" width="${grippieSize}" height="${grippieSize}">
            </rect><rect class="grippie" data-position="right" width="${grippieSize}" height="${grippieSize}">
            </rect><rect class="grippie" data-position="top-left" width="${grippieSize}" height="${grippieSize}">
            </rect><rect class="grippie" data-position="top-right" width="${grippieSize}" height="${grippieSize}">
            </rect><rect class="grippie" data-position="bottom-left" width="${grippieSize}" height="${grippieSize}">
            </rect><rect class="grippie" data-position="bottom-right" width="${grippieSize}" height="${grippieSize}">
            </ellipse></g>
            <svg uid="origin-grippie" fill="#00b706" viewBox="0 0 100 100"  width="10" height="10" style="pointer-events: none;visibility:hidden;display:none">
            <rect x="41" width="17" height="100" fill="inherit"></rect>
            <rect x="41" width="17" height="100" fill="inherit" transform="matrix(0, 1, -1, 0, 100, 0.5)"></rect>
        </svg>
        </g>
        </g>
        <g id="rotate-hud" style="display:none;">
            <g uid="grippies"><rect rx="999" y="0" x="0" width="${grippieSize}" height="${grippieSize}" fill="red"></g>
        </g>
    </g>
</g>`;
};

export default layout;
