import Draggable from '../dependencies/draggable';
import { Stage } from '..';

class Pan {
  #stage: Stage;
  #draggable?: Draggable;
  #startX = 0;
  #startY = 0;
  constructor(stage: Stage) {
    if (!stage) throw new Error('stage not found!');
    this.#stage = stage;
  }
  #install = () => {
    this.#draggable = new Draggable();
    this.#draggable.install({
      element: this.#stage.stage as Element,
      start: () => {
        this.#startX = this.#stage.matrix.e;
        this.#startY = this.#stage.matrix.f;
        (this.#stage.stage as HTMLElement).style.cursor = 'grabbing';
      },
      move: (ev: any) => {
        const { a, b, c, d } = this.#stage.matrix;
        const e = this.#startX + ev.dataX;
        const f = this.#startY + ev.dataY;
        this.#stage.canvas?.setAttribute('transform', `matrix(${a},${b},${c},${d},${e},${f})`);
      },
      end: () => {
        (this.#stage.stage as HTMLElement).style.cursor = 'grab';
      },
    });
  };
  disabled = (isDisabled: boolean) => {
    if (isDisabled) {
      this.#draggable?.uninstall();
    } else {
      this.#install();
    }
  };
}
export default Pan;
