import "./index.css";
import React from "react";
import Fill from "./fill";
import Stroke from "./stroke";
import Typography from "./typography";
import Geometry from "./geometry";
import Arrangement from "./arrangement";
import Objects from "./objects";
import Export from "./export";
import History from "./history";

interface State {
  currenAction: string | undefined;
}

interface Props {}

const actions = [
  {
    value: "Fill",
    href: "#fill-panel",
  },
  {
    value: "Stroke",
    href: "#stroke-panel",
  },
  {
    value: "Typography",
    href: "#typography-panel",
  },
  {
    value: "Geometry",
    href: "#geometry-panel",
  },
  {
    value: "Arrangement",
    href: "#arrangement-panel",
  },
  {
    value: "Objects",
    href: "#objects-panel",
  },
  // {
  //   value: "Export",
  //   href: "#export-panel",
  // },
  {
    value: "History",
    href: "#history-panel",
  },
  {
    value: "Box",
    href: "#3d-box",
  },
];

class Actionbar extends React.Component {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      currenAction: "",
    };
  }

  selectAction = (ev: MouseEvent) => {
    const { target } = ev;
    const t = target as Element;
    const toolbar = document.querySelector("#actionbar");
    const xb = toolbar?.querySelector("x-buttons");
    xb?.childNodes.forEach((node: ChildNode) => {
      (node as Element).removeAttribute("toggled");
    });
    if (t.textContent === this.state.currenAction) {
      this.setState({
        currenAction: "",
      });
    } else {
      t.setAttribute("toggled", "");
      this.setState({
        currenAction: t.textContent as string,
      });
    }
  };

  componentDidMount() {}
  componentWillUnmount() {}

  render = () => {
    return (
      <div id="actionbar" className="flex">
        <x-buttons id="buttons" vertical="">
          {actions.map((item, index) => (
            <x-button
              key={index}
              skin="dock"
              value={item.value}
              onClick={(e: any) => this.selectAction(e)}
            >
              <x-icon href={item.href}></x-icon>
              <x-tooltip>
                <x-message>{item.value}</x-message>
              </x-tooltip>
            </x-button>
          ))}
        </x-buttons>
        <div
          className="compositingpanel"
          style={{
            transform: this.state.currenAction
              ? "translateX(0)"
              : "translateX(281px)",
          }}
        >
          <div
            style={{
              height: "100%",
              display: this.state.currenAction === "Fill" ? "block" : "none",
            }}
          >
            <Fill />
          </div>
          <div
            style={{
              height: "100%",
              display: this.state.currenAction === "Stroke" ? "block" : "none",
            }}
          >
            <Stroke />
          </div>
          <div
            style={{
              height: "100%",
              display:
                this.state.currenAction === "Typography" ? "block" : "none",
            }}
          >
            <Typography />
          </div>
          <div
            style={{
              height: "100%",
              display:
                this.state.currenAction === "Geometry" ? "block" : "none",
            }}
          >
            <Geometry />
          </div>

          <div
            style={{
              height: "100%",
              display:
                this.state.currenAction === "Arrangement" ? "block" : "none",
            }}
          >
            <Arrangement />
          </div>
          <div
            style={{
              height: "100%",
              display: this.state.currenAction === "Objects" ? "block" : "none",
            }}
          >
            <Objects />
          </div>

          {this.state.currenAction === "Export" && <Export />}

          <div
            style={{
              height: "100%",
              display: this.state.currenAction === "History" ? "block" : "none",
            }}
          >
            <History />
          </div>
        </div>
      </div>
    );
  };
}

export default Actionbar;
