import "./index.css";
import React from "react";
import { Stage } from "../../lux";

interface State {
  stage: Stage | undefined;
}

interface Props {}

let openSmatr = true;

class Menus extends React.Component {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      stage: void 0,
    };
  }

  openSvg = async () => {
    const stage = (window as any).stage as Stage;
    const pickerOpts = {
      types: [
        {
          description: "svg",
          accept: {
            "svg/*": [".svg"],
          },
        },
      ],
      excludeAcceptAllOption: true,
      multiple: false,
    };
    const picker = await (window as any).showOpenFilePicker(pickerOpts);
    const f = (await picker[0].getFile()) as File;
    const reader = new FileReader();
    reader.readAsText(f);
    reader.onload = (res: any) => {
      const board = document.querySelector("#board");
      const { height: h } = board?.getBoundingClientRect() as DOMRect;
      const domparser = new DOMParser();
      const doc = domparser.parseFromString(res.target.result, "text/html");
      const svg = doc.querySelector("svg");
      const { width, height } = svg?.viewBox.baseVal as SVGRect;
      (stage.svgRoot as any).innerHTML = svg?.innerHTML;
      stage.setViewSize(width, height);
      stage.setBoardSize(Number(h - 40) / height, 20, 20);
    };
  };

  saveSvg = async () => {
    const stage = (window as any).stage as Stage;
    const opts = {
      types: [
        {
          description: "Svg file",
          accept: { "svg/image": [".svg"] },
        },
      ],
    };

    const handle = await (window as any).showSaveFilePicker(opts);
    const writable = await handle.createWritable();
    const bgo = document.querySelector("#background-outlines rect");
    const x = Number(bgo?.getAttribute("x"));
    const y = Number(bgo?.getAttribute("y"));
    const w = Number(bgo?.getAttribute("width"));
    const h = Number(bgo?.getAttribute("height"));
    const svg = `<svg
    viewBox="${x} ${y} ${w} ${h}"
    width="${w}"
    height="${h}"
    xmlns="http://www.w3.org/2000/svg"
    xmlns:xlink="http://www.w3.org/1999/xlink"
  >${(stage.svgRoot as any).innerHTML}</svg>`;
    await writable.write(svg);
    await writable.close();
  };

  toggleSmartGuides = () => {
    const smart = document.querySelector("#smart");
    smart?.toggleAttribute("toggled");
    const stage = (window as any).stage as Stage;
    stage.select?.toggleSmartGuides((openSmatr = !openSmatr));
  };

  toggleMaunalGuides = () => {
    const maunal = document.querySelector("#maunal");
    maunal?.toggleAttribute("toggled");
    const ruler = document.querySelector("#lux-rulers") as HTMLElement;
    if (ruler.style.display === "none") {
      ruler.style.display = "block";
    } else {
      ruler.style.display = "none";
    }
  };

  action = (action: string) => {
    const stage = (window as any).stage as Stage;
    switch (action) {
      case "open":
        this.openSvg();
        break;
      case "save":
        this.saveSvg();
        break;
      case "smart":
        this.toggleSmartGuides();
        break;
      case "maunal":
        this.toggleMaunalGuides();
        break;
      case "undo":
        stage.history?.undo();
        break;
      case "redo":
        stage.history?.redo();
        break;
      case "cut":
        stage.clipboard?.cut();
        break;
      case "copy":
        stage.clipboard?.copy();
        break;
      case "paste":
        stage.clipboard?.paste();
        break;
    }
  };

  componentDidMount() {}
  componentWillUnmount() {}

  render() {
    return (
      <header id="header">
        <x-menubar>
          <x-menuitem>
            <x-label>File</x-label>

            <x-menu>
              <x-menuitem onClick={() => this.action("open")}>
                <x-label>Open</x-label>
              </x-menuitem>

              <hr />

              <x-menuitem onClick={() => this.action("save")}>
                <x-label>Save As..</x-label>
              </x-menuitem>
            </x-menu>
          </x-menuitem>

          <x-menuitem>
            <x-label>Edit</x-label>

            <x-menu>
              <x-menuitem onClick={() => this.action("undo")}>
                <x-label>Undo</x-label>
                <x-shortcut value="Control+Z"></x-shortcut>
              </x-menuitem>

              <x-menuitem onClick={() => this.action("redo")}>
                <x-label>Redo</x-label>
                <x-shortcut value="Control+Shift+Z"></x-shortcut>
              </x-menuitem>

              <hr />

              <x-menuitem onClick={() => this.action("cut")}>
                <x-label>Cut</x-label>
                <x-shortcut value="Control+X"></x-shortcut>
              </x-menuitem>

              <x-menuitem onClick={() => this.action("copy")}>
                <x-label>Copy</x-label>
                <x-shortcut value="Control+C"></x-shortcut>
              </x-menuitem>

              <x-menuitem onClick={() => this.action("paste")}>
                <x-label>Paste</x-label>
                <x-shortcut value="Control+V"></x-shortcut>
              </x-menuitem>
            </x-menu>
          </x-menuitem>

          <x-menuitem>
            <x-label>View</x-label>

            <x-menu>
              <x-menuitem
                id="maunal"
                onClick={() => this.action("maunal")}
                toggled="true"
              >
                <x-label>Maunal Guides</x-label>
              </x-menuitem>

              <x-menuitem
                id="smart"
                onClick={() => this.action("smart")}
                toggled="true"
              >
                <x-label>Smart Guides</x-label>
              </x-menuitem>
            </x-menu>
          </x-menuitem>

          <x-menuitem>
            <x-label>Help</x-label>

            <x-menu>
              <x-menuitem>
                <x-label>wechat:13696977423</x-label>
              </x-menuitem>
            </x-menu>
          </x-menuitem>
        </x-menubar>
      </header>
    );
  }
}

export default Menus;
